<template>
    <div class="vue-editor">
        <div class="quillWrapper">
            <slot name="toolbar"></slot>
            <div :id="id" ref="quillContainer"></div>
            <input v-if="useCustomImageHandler" @change="emitImageInfo($event)" ref="fileInput" id="file-upload" type="file" accept="image/*" style="display:none;">
        </div>

        <div class="placeholder-html" v-html="placeholderHtml" v-if="showPlaceholder"></div>

        <a href="javascript:void(0);" class="link-icon set-template-link" @click="setText(templateDefault)" v-if="showPlaceholder && templateDefault">
            <span class="i i-pencil3"></span>
            <span class="text text-size-s">Вставить шаблон</span>
        </a>

        <input style="display: none;" ref="fileGallery" @change="saveGallery($event.target.files);" type="file" name="gallery_files" class="gallery_files" id="" multiple="" accept="image/jpeg,image/png,image/gif">

        <popup :show="popup.gallery.show"
               @close="popup.gallery.show = false"
                           :width="635">
            <div class="text-size-xl text-color-red text-bolder">
                Ошибка загрузки!
            </div>

            <div class="popup__content">
                <p>Допускается не более 20 файлов.  То есть, мы загрузим первые 20, а остальные – нет...</p>
            </div>

            <div class="popup__footer">
                <a href="javascript:;" @click="saveGallery(true);" class="btn btn-white">Продолжить</a>
                <a @click="popup.gallery.show = false" href="javascript:;" class="pseudo-link ml">Отмена</a>
            </div>
        </popup>
    </div>
</template>

<script>
    import Quill from 'quill'
    import Popup from '@/components/modal/Popup.vue';
    import {mapState} from 'vuex';
    import axios from 'axios'
    import {fileHeader} from '@/helpers/axiosHeaders'
    import qs from "qs";

    export default {
        name: "TextEditor",
        components: {
            Popup,
        },
        props: {
            id: {
                type: String,
                default: "quill-container"
            },
            placeholder: {
                type: String,
                default: ""
            },
            placeholderHtml: {
                type: String,
                default: ""
            },
            templateDefault: {
                type: String,
                default: ""
            },
            value: {
                type: String,
                default: ""
            },
            hideToolbar: {
                type: Boolean,
                default: false
            },
            setTargetBlank: {
                type: Boolean,
                default: false
            },
            disabled: {
                type: Boolean
            },
            editorToolbar: Array,
            editorOptions: {
                type: Object,
                required: false,
                default: function _default() {
                    return {};
                }
            },
            useCustomImageHandler: {
                type: Boolean,
                default: false
            }
        },

        data: function() {
            return {
                quill: null,
                popup: {
                    gallery: {
                        show: false,
                        value: false,
                        files: []
                    }
                },
                showPlaceholder: false,
                isSend: false
            };
        },

        mounted: function() {
            this.registerPrototypes();
            this.initializeEditor();
            let self = this;
            this.quill.root.addEventListener('blur', function () {
                self.$emit("blur");
            });
        },


