                                        if(currentLeaf.domNode.innerHTML == '<br>'){
                                            this.quill.deleteText(range.index, 2, 'user');
                                        } else {
                                            return true;
                                        }
                                    }
                                },
                                linebreak: {
                                    key: 13,
                                    //shiftKey: true,
                                    handler: function handler(range) {
                                        var currentLeaf = this.quill.getLeaf(range.index)[0];
                                        var nextLeaf = this.quill.getLeaf(range.index + 1)[0];

                                        this.quill.insertEmbed(range.index, 'break', true, 'user');

                                        // Insert a second break if:
                                        // At the end of the editor, OR next leaf has a different parent (<p>)
                                        if (nextLeaf === null || currentLeaf.parent !== nextLeaf.parent) {
                                            this.quill.insertEmbed(range.index, 'break', true, 'user');
                                        }

                                        // Now that we've inserted a line break, move the cursor forward
                                        this.quill.setSelection(range.index + 1, Quill.sources.SILENT);
                                    }
                                },
                            }
                        },
                        toolbar: {
                            container: this.editorToolbar ? this.editorToolbar : [
                                [{header: [false, 1, 2, 3, 4, 5, 6]}],
                                ["bold", "italic", "underline", "strike"], // toggled buttons
                                [{align: ""}, {align: "center"}, {align: "right"}, {align: "justify"}], ["blockquote", "code-block"],
                                [{list: "ordered"}, {list: "bullet"}, {list: "check"}], [{indent: "-1"}, {indent: "+1"}], // outdent/indent
                                [{color: []}, {background: []}], // dropdown with defaults from theme
                                ["link", "image", "video"], ["clean"] // remove formatting button
                            ],
                            handlers: {
                                gallery: function() {
                                    self.$refs.fileGallery.click();
                                }
                            }
                        }
                    };
                }
                return result;
            },
            togglePopup: function (popup) {
                if(this.popup[popup]){
                    if(this.popup[popup].show) {
                        this.popup[popup].value = false;
                        this.popup[popup].show = false;
                    } else {
                        this.popup[popup].show = true;
                    }
                }
            },

            saveGallery: function (files,force) {
                force = force || false;
                let form_data = new FormData();
                let count = 0;
                let self = this;
                for(let index in files) {
                    if(count <= 20){
                        form_data.append(index, files[index]);
                    }
                    count++;
                }
                if(!force && count > 20){
                    this.popup.gallery.show = true;
                } else {
                    this.popup.gallery.show = false;

                    if(!this.isSend){
                        let rangeLoader = self.quill.getSelection();

                        let loader =  self.quill.insertEmbed(rangeLoader.index, 'loader', '<div class="preloader-page">' +
                            '<style>@import url(/css/img_thumbnails.css?v125);</style>' +
                            '<div class="preloader"></div>' +
                            '</div>');
                        self.quill.enable(false);
                        this.isSend = true;
                        axios.post(process.env.VUE_APP_API_PATH+'/user/galleries/',form_data,{
                            headers: {'Content-Type': 'multipart/form-data','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                            withCredentials: true,

                        }).then(response => {
                            self.isSend = false;
                            if(typeof response.data === 'object' && +response.data.error === 0) {
                                let data = response.data.result;
                                if(parseInt(data.id) > 0) {
                                    let range = self.quill.getSelection();
                                    self.quill.deleteText(rangeLoader.index,2);
                                    if (range) {
                                            let html = '<section id="surrogate_gallery_'+data.id+'">' +
                                            '<style>@import url(/css/img_thumbnails.css?v125);</style>' +
                                            data.html +
                                            '</section>';
                                        self.quill.insertEmbed(range.index, 'gallery', html);
                                    }
                                    self.quill.enable();
                                    self.isSend = false;
                                }
                            } else {
                                if( +response.data.error===401){
                                    this.$store.dispatch('auth/logout')
                                }
                            }
                        }).catch(function (error) {
                            self.isSend = false;
                        });
                    }
                }

            },
