                            node.setAttribute('srcdoc', value);
                            node.setAttribute('frameborder', '0');
                            node.setAttribute('allowfullscreen', true);
                            node.setAttribute('width', '100%');
                            node.setAttribute('class', 'gallery-frame');
                            if(value.indexOf("col-24") !== -1){
                                node.setAttribute('height', '510');
                            } else if(value.indexOf("col-12") !== -1){
                                node.setAttribute('height', '255');
                            } else if(value.indexOf("col-8") !== -1){
                                node.setAttribute('height', '170');
                            } else if(value.indexOf("col-6") !== -1){
                                node.setAttribute('height', '130');
                            }
                            return node;
                        }


                    }, {
                        key: 'value',
                        value: function value(node) {
                            return node.getAttribute('srcdoc');
                        }
                    }]);

