                            if(html.length === 0) {
                                html = e.clipboardData.getData('text')
                            }
                            let newEl = document.createElement('div');
                            newEl.innerHTML = html;

                            if(newEl.querySelectorAll('*')){
                                Array.prototype.forEach.call(newEl.querySelectorAll('*'), function(element){
                                    element.removeAttribute('style');
                                });
