            prepareEditorConfig: function (editorConfig) {
                if (Object.keys(this.editorOptions).length > 0 && this.editorOptions.constructor === Object) {
                    if (this.editorOptions.modules && typeof this.editorOptions.modules.toolbar !== "undefined") {
                        // We don't want to merge default toolbar with provided toolbar.
                        delete editorConfig.modules.toolbar;
                    }
                    lodash.merge(editorConfig, this.editorOptions);
                }
            },
            registerPrototypes: function () {
                Quill.prototype.getHTML = function () {
                    return this.container.querySelector(".ql-editor").innerHTML;
                };
                Quill.prototype.getWordCount = function () {
                    return this.container.querySelector(".ql-editor").innerText.length;
                };
            },
            registerEditorEventListeners: function () {
                this.quill.on("text-change", this.handleTextChange);
                this.quill.on("selection-change", this.handleSelectionChange);
                this.listenForEditorEvent("text-change");
                this.listenForEditorEvent("selection-change");
                this.listenForEditorEvent("editor-change");
            },
            listenForEditorEvent: function (type) {
                let _this = this;

                this.quill.on(type, function() {
                    for (var _len = arguments.length, args = Array(_len), _key = 0; _key < _len; _key++) {
                        args[_key] = arguments[_key];
                    }

                    _this.$emit.apply(_this, [type].concat(args));
                });
            },
            handleInitialContent: function () {
                if (this.value) this.quill.root.innerHTML = this.value; // Set initial editor content
            },
            handleSelectionChange: function (range, oldRange) {
                if (!range && oldRange) this.$emit("blur", this.quill);else if (range && !oldRange) this.$emit("focus", this.quill);
            },
            handleTextChange: function() {
                let editorContent = this.quill.getHTML() === "<p><br></p>" ? "" : this.quill.getHTML();
                if(editorContent.length > 0) {
                    this.showPlaceholder = false;
                } else {
                    this.showPlaceholder = true;
                }
                this.$emit("input", editorContent);
            },
            checkForCustomImageHandler: function() {
                this.useCustomImageHandler === true ? this.setupCustomImageHandler() : "";
            },
            setupCustomImageHandler: function() {
                let toolbar = this.quill.getModule("toolbar");
                toolbar.addHandler("image", this.customImageHandler);
            },
            customImageHandler: function(image, callback) {
                this.$refs.fileInput.click();
            },
            emitImageInfo: function($event) {
                let resetUploader = function resetUploader() {
                    let uploader = document.getElementById("file-upload");
                    uploader.value = "";
                };
                let file = $event.target.files[0];
                let Editor = this.quill;
                let range = Editor.getSelection();
                let cursorLocation = range.index;
                this.$emit("imageAdded", file, Editor, cursorLocation, resetUploader);
            },
            setText: function (text) {
                text = text.replace (/[\n\r]/g, '');
                let arText = text.split('<br>');
                for(let i in arText){
                    arText[i] = arText[i].trim();
                }
                text = arText.join('<br>');
                this.quill.pasteHTML(0, text);
                if(text.length > 0) {
                    this.showPlaceholder = false;
                }
            }

        },

        watch: {
            value: function(val) {
                if (val != this.quill.root.innerHTML && !this.quill.hasFocus()) {
                    let text = val.replace (/[\r]/g, '');
                    text = text.replace (/[\n]/g, '');
                    let arText = text.split('<br>');
                    for(let i in arText){
                        arText[i] = arText[i].trim();
                    }
                    text = arText.join('<br>');
                    this.quill.pasteHTML(0, text);
                    let content = this.quill.getContents(this.quill.getLength()-2);
                    if(content.ops.length > 0 && content.ops[0].insert == "\n\n"){
                        this.quill.deleteText(this.quill.getLength()-2,2);
                    }
                    this.$nextTick(function() {
                        this.quill.enable(true);
                        this.quill.blur();
                        /*if(!this.hideToolbar) {
                            window.scroll(0,0);
                        }*/
                        this.$emit("set_value", this.quill);
                    });
                    if(val.length > 0) {
                        this.showPlaceholder = false;
                    } else {
                        this.showPlaceholder = true;
                    }

                }
            },
            disabled: function(status) {
                this.quill.enable(!status);
            }
