import axios from 'axios'
import {jsonHeader, wwwFormHeader} from '@/helpers/axiosHeaders'
import Vue from 'vue'
import qs from "qs";

const state = {
    items: [],
    isLoad: false,
    isFormLoad: false,
}
const getters = {}
const mutations = {
    setItem(state, data) {
        if (typeof data === 'object' && data.item !== undefined) {
            state.items.unshift(data.item);
        }
    },
    setItems(state, data) {
        state.items = [];
        if (typeof data === 'object' && data.items !== undefined) {
            for (let itemIndex in data.items) {
                let item = data.items[itemIndex];
                state.items.push(item);
            }
        }
    },
    setLoad(state, data) {
        state.isLoad = data;
    },
    setFormLoad(state, data) {
        state.isFormLoad = data;
    },
}
const actions = {
    getItems(context, data) {
        if (!state.isLoad) {
            context.commit('setItems', {});
            context.commit('setLoad', true);
            axios.get(process.env.VUE_APP_API_PATH+'/moderator/notes/', {
                headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                withCredentials: true,
                params: {
                    entity_id: data.entity_id,
                    type: data.type,
                    notes: true,
                },
                paramsSerializer: function (params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
            }).then(response => {
                context.commit('setLoad', false);
                if (typeof response.data === 'object' && +response.data.error === 0) {
                    context.commit('setItems', response.data.result);
                } else {
                    if( +response.data.error===401){
                        context.dispatch('auth/logout',{},{root:true})
                    }
                }
            }).catch(function (error) {
                context.commit('setLoad', false);
            });
        }
    },
    addItem(context, data) {
        return new Promise((resolve, reject) => {
            if (!state.isFormLoad) {
                context.commit('setFormLoad', true);
                axios.post(process.env.VUE_APP_API_PATH+'/moderator/notes/',{}, {
                    headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                    withCredentials: true,
                    params: {
                        entity_id: data.entity_id,
                        type: data.type,
                        text: data.text,
                        notes: true,
                    },
                    paramsSerializer: function (params) {
                        return qs.stringify(params, {arrayFormat: 'repeat'})
                    },
                }).then(response => {
                    if (typeof response.data === 'object' && +response.data.error === 0) {
                        context.commit('setItem', response.data.result);
                        context.commit('setFormLoad', false);
                        resolve();
                    } else {
                        if( +response.data.error===401){
                            context.dispatch('auth/logout',{},{root:true})
                        }
                    }
                }).catch(function (error) {
                    context.commit('setFormLoad', false);
                });
            }
        });
    },
}
export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}