<template>
    <div class="">
        <div class="category-tree">
            <div class="category-tree__header">
                <div class="wrap-left">
                    <div class="category-tree__search">
                        <span class="i icon-left i-search"></span>

                        <input type="search" placeholder="Горячий поиск"
                               ref="search"
                               id="search"
                               @input="searchItems"
                               v-model.trim="searchText">

                        <span class="field-clear__btn" style="display: none" @click="clearSearchInput">
							<span class="i i-close2"></span>
						</span>
                    </div>
                </div>


                <div class="wrap-right category-tree__filter ">
                    <span @click="actionAddSection" class="category-tree__filter__item has-hint">
                        <span class="i i-plus2"></span>

                        <span class="hint bottom-left-direction">Добавить раздел</span>
                    </span>

                    <span v-if="false" class="category-tree__filter__item">
                        <span class="i i-info"></span>
                    </span>
                    
                    <div class="category-tree__filter__view" data-btn-group="group1">
                        <input id="btn_hide_all" v-model="btnView.hide_all" type="checkbox">
                        <label @click="changeViewMode('hide_all')" for="btn_hide_all" class="has-hint"><span class="i i-list2"></span>
                            <span class="hint bottom-left-direction">Свернуть всё</span>
                        </label>

                        <input id="btn_hide_level2" v-model="btnView.level2" type="checkbox">
                        <label @click="changeViewMode('hide_level2')" for="btn_hide_level2" class="has-hint"><span class="i i-list3"></span>
                            <span class="hint bottom-left-direction">Раскрыть до 2-го уровня</span>
                        </label>
                        
                        <input id="btn_hide_level3" v-model="btnView.level3" type="checkbox">
                        <label @click="changeViewMode('hide_level3')" for="btn_hide_level3" class="has-hint"><span class="i i-list4"></span>
                            <span class="hint bottom-left-direction">Раскрыть до 3-го уровня</span>
                        </label>
                        
                        <input id="btn_restore" v-model="btnView.restore" type="checkbox">
                        <label @click="changeViewMode('restore')" for="btn_show_all" class="has-hint"><span class="i i-list5"></span>
                            <span class="hint bottom-left-direction">Раскрыть как было</span>
                        </label>
                    </div>
                </div>
            </div>
            <!-- end category-tree__header -->



            <div class="category-tree__list">
                <div id="catalog">
                    <div class="category-tree__item" v-show="isAddSection">
                        <div class="title title--edit">
                            <div class="wrap">
                                <div class="inp-wrap">
                                    <input ref="addSection" @keyup.enter="createSection" @keyup.esc="cancelAddSection" type="text" v-model.trim="newSectionName" class="inp size-s full"
                                           placeholder="Введите название раздела">
                                </div>

                                <div class="right">
                                    <a href="#" @click="createSection" class="btn btn--white btn--icon size-s w40">
                                        <span class="i i-ok2"></span>
                                    </a>

                                    <a href="#" @click="cancelAddSection" class="btn btn--white btn--icon size-s w40">
                                        <span class="i i-close4"></span>
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                    
                    
                    <categories-items v-if="visibleSections.length" :list="sections" :itemId="itemId">
                    </categories-items>
                    
                    
                    <div v-if="!visibleSections.length && sections.length" class="category-tree__search-result sect-blank">

                        <div class="wrap text-center">
                            <div class="text-size-l text-color-grey text-bolder">К сожалению, по вашему запросу<br>ничего не найдено...</div>
                        </div>

                        <div class="mini-zems"></div>
                    </div>
                </div>
                <!-- end category-tree__list -->
            </div>
            <!-- end category-tree -->
        </div>

        <popup @close="modalCancelCreate" :isBtnClose="false" :show="popups.categoriesCreateConfirm.show" :width="640">
            <categories-create :count="countCoupons" @apply="modalApplyCreate" @cancel="modalCancelCreate"></categories-create>
        </popup>

        <popup @close="modalCancelDrag" :show="popups.categoriesMoveConfirm.show" :width="640">
            <categories-move :count="countCoupons" @apply="modalApplyDrag" @cancel="modalCancelDrag"></categories-move>
        </popup>

        <popup @close="modalCancelHide" :show="popups.categoriesHideConfirm.show" :width="640">
            <categories-hide :countActions="countCoupons" :countSections="countSections" @apply="modalApplyHide" @cancel="modalCancelHide"></categories-hide>
        </popup>

        <popup @close="modalCancelDelete" :show="popups.categoriesDeleteConfirm.show" :width="640">
            <categories-delete :countActions="countCoupons" :countSections="countSections" @apply="modalApplyDelete" @cancel="modalCancelDelete"></categories-delete>
        </popup>

    </div>


</template>


<script>

    import {mapState} from 'vuex';
    import axios from 'axios';
    import draggable from '@/helpers/vuedraggable.js';
    import CategoriesItems from '@/components/moderator/CategoriesItems.vue';
    import CategoriesCreate from '@/components/modal/CategoriesCreate.vue';
    import CategoriesMove from '@/components/modal/CategoriesMove.vue';
    import CategoriesHide from '@/components/modal/CategoriesHide.vue';
    import CategoriesDelete from '@/components/modal/CategoriesDelete.vue';
    import Popup from '@/components/modal/Popup.vue';
    import Loader from '@/components/Loader.vue';
    import qs from "qs";

    export default {
        name: "Categories",
        components: {
            draggable,
            CategoriesItems,
            CategoriesCreate,
            CategoriesMove,
            CategoriesHide,
            CategoriesDelete,
            Popup
        },
        props: {
            componentName: {'default': 'Categories'}
        },
        data() {
            return {
                sections: [],
                tmpViewStateSections: [],
                searchText: '',
                isShowClearSearchBtn: false,
                isAddSection: false,
                newSectionName: '',
                sections_id: [],
                tmpEvent: [],
                tmpPrevID: -1,
                tmpNextID: -1,
                tmpCreatedID: -1,
                tmpHideID: -1,
                tmpDeleteID: -1,
                countCoupons: 0,
                countSections: 0,
                showModalDrag: false,
                showModalCreate: false,
                setLoad: false,
                formDataUpdateSections: {},
                btnView: {
                    hide_all: false,
                    hide_level2: false,
                    hide_level3: false,
                    restore: false,
                    show_all: false,
                },
                openSectionInterval: 0
            };
        },
        watch: {
        },
        mounted: function() {
            this.getToken();
            this.getSections();
            this.setFocus('search');
        },
        computed: {
            token(){
                return this.localStorage.token !== 'false' && this.localStorage.token !== null && this.localStorage.token !== undefined ? this.localStorage.token : false
            },
            ...mapState({
                itemId: state => state.moderatorCategories.itemId,
                popups: state => state.popups.items,
            }),
            visibleSections: function () {
                var sections_filter = this.sections.filter(section => section.VISIBLE === true);
                return sections_filter;
            },
        },
        methods: {
            getCountSubSections(section) {
                let count = section.SECTIONS.length;

                for (let ind in section.SECTIONS) {
                    count += this.getCountSubSections(section.SECTIONS[ind]);
                }

                return count;
            },
            getCountCoupons(section) {
                let count = section.COUPONS;

                for (let ind in section.SECTIONS) {
                    count += this.getCountCoupons(section.SECTIONS[ind]);
                }

                return count;
            },
            setFocus(ref) {
                if(!!this.$refs[ref]){
                    let input = this.$refs[ref];

                    if(input){
                        input.focus()
                        setTimeout(function () {
                            input.select()
                        },20)
                    }
                }
            },
            getToken() {
                this.$store.dispatch('moderatorCategories/getToken',{isOrder:false});
            },
            setLoadStatus: function(status) {
                let self = this;
                self.$store.commit('moderatorCategories/setLoadStatus', status);
                if (status === 'ok' || status === 'error') {
                    setTimeout(function () {
                        self.$store.commit('moderatorCategories/setLoadStatus', 'none');
                    }, 2000);
                }
            },
            getSections: function () {
                let self = this;

                //this.saveViewStateSections(self.sections);
                axios.get(process.env.VUE_APP_API_PATH+'/moderator/categories/', {
                    headers: {
                        'Content-Type': 'application/json;charset=utf-8',
                        'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                    },
                    withCredentials: true,
                    params: {
                        action: 'GetCatalog',
                        id: self.itemId
                    },
                    paramsSerializer: function (params) {
                        return qs.stringify(params, {arrayFormat: 'repeat'})
                    },
                }).then(response => {
                    if( +response.data.error===401){
                        this.$store.dispatch('auth/logout')
                    } else {
                        let sections = self.reformatSections(response.data.result.sections);
                        self.sections = [];
                        for (let index in sections) {
                            self.sections.push(sections[index]);
                        }
                    }
                    //self.sections = self.restoreViewStateSections(self.sections);

                }).catch(function (error) {
                });


            },
            getParent: function (sections, id, section) {
                let result = false;
                for (let ind in sections) {
                    if (sections[ind].ID === id) {
                        result = section;
                    } else {
                        result = this.getParent(sections[ind].SECTIONS, id, sections[ind]);
                    }

                    if (result !== false) {
                        break;
                    }
                }
                return result;
            },
            stateSection: function (sections, id, code, name = '', is_active = true) {
                for (let ind in sections) {
                    if (sections[ind].ID == id) {
                        if (code == 'OPENED') {
                            if (sections[ind].SECTIONS.length) {
                                sections[ind].OPENED = !sections[ind].OPENED;
                            }
                        }
                        if (code == 'FOCUSED') {
                            sections[ind].FOCUSED = true;
                        }
                        if (code == 'UNFOCUSED') {
                            sections[ind].FOCUSED = false;
                        }
                        if (code == 'CREATE') {
                            let tmpId = new Date().getTime(),
                                section = {
                                    ID: tmpId,
                                    CREATED: true,
                                    COUPONS: 0,
                                    NAME: name,
                                    VISIBLE: true,
                                    ACTIVE: is_active ? 'Y' : 'N',
                                    SECTIONS: [],
                                    IS_EDIT: name === '',
                                };

                            sections[ind].SECTIONS.unshift(section);

                            sections[ind].OPENED = true;
                        }
                        if (code == 'SAVED_NEW') {
                            let tmpId = new Date().getTime(),
                                section = {
                                    ID: tmpId,
                                    CREATED: false,
                                    COUPONS: 0,
                                    NAME: name,
                                    VISIBLE: true,
                                    ACTIVE: is_active ? 'Y' : 'N',
                                    SECTIONS: [],
                                    IS_EDIT: name === '',
                                };

                            sections[ind].SECTIONS.unshift(section);

                            sections[ind].OPENED = true;
                        }
                        if (code == 'UNACTIVE') {
                            sections[ind].ACTIVE = sections[ind].ACTIVE == 'N' ? 'Y' : 'N';
                        }
                        if (code == 'EDIT') {
                            sections[ind].IS_EDIT = !sections[ind].IS_EDIT;

                            if (sections[ind].CREATED && !sections[ind].NAME) {
                                sections.splice(ind, 1);
                                continue;
                            }
                        }
                        if (code == 'NAME') {
                            if (sections[ind].TMP_NAME != '') {
                                sections[ind].NAME = sections[ind].TMP_NAME;
                            }

                            if (sections[ind].CREATED && !sections[ind].NAME) {
                                sections.splice(ind, 1);
                                continue;
                            }
                        }
                        if (code == 'DELETED') {
                            sections[ind].DELETED = sections[ind].DELETED != 'Y' ? 'Y' : 'N';
                        }
                    }
                    sections[ind].SECTIONS = this.stateSection(sections[ind].SECTIONS, id, code);
                }

                return sections;
            },
            modalApplyDrag: function (data) {
                let tmpId = new Date().getTime(),
                    parentSection = this.getParent(this.sections, this.tmpEvent.draggedContext.element.ID, []),
                    section = {
                        ID: tmpId,
                        CREATED: true,
                        COUPONS: 0,
                        NAME: data.sectionName,
                        VISIBLE: true,
                        ACTIVE: 'Y',
                        SECTIONS: [],
                        IS_EDIT: false,
                    };

                this.$set(this, 'sections', this.pasteSection(this.sections, parentSection.ID, section));
                this.updateSections();
                this.showModalDrag = false;
                this.$store.commit('popups/close', {name: this.popups.categoriesMoveConfirm.name});
            },
            modalCancelDrag: function () {
                this.dragCancel();
                this.showModalDrag = false;
                this.$store.commit('popups/close', {name: this.popups.categoriesMoveConfirm.name});
            },
            modalCancelHide: function () {
                this.tmpHideID = -1;
                this.$store.commit('popups/close', {name: this.popups.categoriesHideConfirm.name});
            },
            modalCancelDelete: function () {
                this.tmpDeleteID = -1;
                this.$store.commit('popups/close', {name: this.popups.categoriesDeleteConfirm.name});
            },
            modalApplyHide: function () {
                if (this.tmpHideID > -1) {
                    this.$set(this, 'sections', this.stateSection(this.sections, this.tmpHideID, 'UNACTIVE'));
                    this.updateSections();
                    this.tmpHideID = -1;
                }
                this.$store.commit('popups/close', {name: this.popups.categoriesHideConfirm.name});
            },
            modalApplyDelete: function () {
                if (this.tmpDeleteID > -1) {
                    this.stateSection(this.sections, this.tmpDeleteID, 'DELETED');
                    this.updateSections();

                    this.tmpDeleteID = -1;
                }
                this.$store.commit('popups/close', {name: this.popups.categoriesDeleteConfirm.name});
            },
            modalApplyCreate: function (data) {
                let tmpId = new Date().getTime(),
                    parentSection = this.getParent(this.sections, this.tmpCreatedID, []),
                    section = {
                        ID: tmpId,
                        CREATED: true,
                        COUPONS: 0,
                        NAME: data.sectionName,
                        VISIBLE: true,
                        ACTIVE: 'Y',
                        SECTIONS: [],
                        IS_EDIT: false,
                    };

                this.$set(this, 'sections', this.pasteSection(this.sections, parentSection.ID, section));

                this.updateSections();
                this.showModalCreate = false;
                this.$store.commit('popups/close', {name: this.popups.categoriesCreateConfirm.name});
            },
            modalCancelCreate: function () {
                this.removeSection(this.sections, this.tmpCreatedID);
                this.showModalCreate = false;
                this.$store.commit('popups/close', {name: this.popups.categoriesCreateConfirm.name});
            },
            dragCancel: function () {
                this.$set(this, 'sections', this.removeSection(this.sections, this.tmpEvent.draggedContext.element.ID));
                this.$set(this, 'sections', this.addSectionBetween(this.sections, this.tmpPrevID, this.tmpNextID, this.tmpEvent.draggedContext.element));
            },
            reformatSections: function (sections) {
                sections = Object.values(sections);

                for (let ind in sections) {
                    sections[ind].VISIBLE = true;
                    sections[ind].IS_EDIT = false;
                    if (typeof(sections[ind].CHECKED) === 'undefined') {
                        sections[ind].CHECKED = false;
                    }

                    sections[ind].TMP_NAME = sections[ind].NAME;
                    if (!(Object.keys(sections[ind].SECTIONS).length === 0 && typeof(sections[ind].SECTIONS) === 'object')) {
                        sections[ind].SECTIONS = this.reformatSections(sections[ind].SECTIONS);
                        for (let index in sections[ind].SECTIONS) {
                            if (sections[ind].SECTIONS[index].OPENED || sections[ind].SECTIONS[index].CHECKED) {
                                sections[ind].OPENED = true;
                            }
                        }
                    } else {
                        if (!sections[ind].IBLOCK_SECTION_ID) {
                            sections[ind].OPENED = true;
                        }
                    }
                }
                return sections;
            },
            removeSection: function (sections, id) {
                for (let ind in sections) {
                    if (sections[ind].ID === id) {
                        sections.splice(ind, 1);
                        break;
                    } else {
                        sections[ind].SECTIONS = this.removeSection(sections[ind].SECTIONS, id);
                    }
                }

                return sections;
            },
            pasteSection: function (sections, id, section) {
                for (let ind in sections) {
                    let iInd = parseInt(ind);
                    if (sections[ind].ID === id) {
                        sections[ind].SECTIONS.splice(0, 0, section);
                        break;
                    } else {
                        sections[ind].SECTIONS = this.pasteSection(sections[ind].SECTIONS, id, section);
                    }
                }

                return sections;
            },
            addSectionBetween: function (sections, prevId, nextId, section) {
                for (let ind in sections) {
                    let iInd = parseInt(ind);
                    if (prevId != -1 && sections[ind].ID === prevId) {
                        sections.splice(iInd + 1, 0, section);
                        break;
                    } else if (nextId != -1 && sections[ind].ID === nextId) {
                        sections.splice(iInd - 1, 0, section);
                        break;
                    }
                    else {
                        sections[ind].SECTIONS = this.addSectionBetween(sections[ind].SECTIONS, prevId, nextId, section);
                    }
                }

                return sections;
            },
            convertSectionsForRequest: function (sections) {
                let sections_arr = Object.values(sections);

                let sections_new = {};

                for (let key in sections_arr) {
                    sections_new['ID_' + sections_arr[key].ID] = sections_arr[key];
                }

                for (let ind in sections_new) {
                    sections_new[ind].VISIBLE = true;
                    sections_new[ind].IS_EDIT = false;
                    if (typeof(sections_new[ind].CHECKED) === 'undefined') {
                        sections_new[ind].CHECKED = false;
                    }

                    sections_new[ind].TMP_NAME = sections_new[ind].NAME;
                    if (!(Object.keys(sections_new[ind].SECTIONS).length === 0 && typeof(sections_new[ind].SECTIONS) === 'object')) {

                        sections_new[ind].SECTIONS = this.convertSectionsForRequest(sections_new[ind].SECTIONS);

                        for (let index in sections_new[ind].SECTIONS) {
                            if (sections_new[ind].SECTIONS[index].OPENED || sections_new[ind].SECTIONS[index].CHECKED) {
                                sections_new[ind].OPENED = true;
                            }
                        }
                    }
                }
                for (let index in sections_new.SECTIONS) {
                    if (sections_new.SECTIONS[index].OPENED) {
                        sections_new.OPENED = true;
                    }
                }
                return sections_new;
            },
            updateSections: function () {
                let self = this;
                let formData = new FormData();

                self.$set(this, 'setLoad', true);


                let sections = this.convertSectionsForRequest(self.sections);

                formData.append('action', 'UpdateSections');

                formData.append('sections', qs.stringify(sections, {arrayFormat: 'repeat'}));


                this.reformatSections(self.sections);

                this.setLoadStatus('load');


                axios.post(process.env.VUE_APP_API_PATH+'/moderator/categories/', formData, {
                    headers: {
                        'Content-Type': 'application/json;charset=utf-8',
                        'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                    },
                    withCredentials: true,
                }).then(response => {
                    if( +response.data.error===401){
                        this.$store.dispatch('auth/logout')
                    } else {
                        self.setLoadStatus('ok');
                        self.getSections();
                        self.$store.commit('moderatorCategories/setItemSaves', true);
                    }

                }).catch(function (error) {
                    self.setLoadStatus('error');
                });
            },
            getCheckedSections: function (sections) {
                for (let ind in sections) {
                    if (sections[ind].CHECKED) {
                        this.sections_id.push(sections[ind].ID);
                    }

                    sections[ind].SECTIONS = this.getCheckedSections(sections[ind].SECTIONS);
                }

                return sections;
            },
            getPrevId: function (sections, id) {
                let prevId = -1;
                for (let ind in sections) {
                    let iInd = parseInt(ind);
                    if (sections[ind].ID === id) {
                        if (ind != 0) {
                            prevId = sections[iInd - 1].ID;
                        }
                        return prevId;
                    } else {
                        prevId = this.getPrevId(sections[ind].SECTIONS, id);
                    }
                }

                return prevId;
            },
            getNextId: function (sections, id) {
                let nextId = -1;
                for (let ind in sections) {
                    let iInd = parseInt(ind);
                    if (sections[ind].ID === id) {
                        if (ind != sections.length - 1) {
                            nextId = sections[iInd + 1].ID;
                        }
                        return nextId;
                    } else {
                        nextId = this.getPrevId(sections[ind].SECTIONS, id);
                    }
                }

                return nextId;
            },
            getSectionById: function (sections, id) {
                let result = false;
                for (let ind in sections) {
                    if (sections[ind].ID == id) {
                        result = sections[ind];
                    } else {
                        result = this.getSectionById(sections[ind].SECTIONS, id);
                    }

                    if (result !== false) {
                        break;
                    }
                }
                return result;
            },
            updateElement: function () {
                let self = this;
                let formData = new FormData();

                self.sections_id = [];
                self.getCheckedSections(self.sections);

                self.$set(this, 'setLoad', true);


                formData.append('action', 'UpdateElement');
                formData.append('section_id', self.sections_id);
                formData.append('id', this.itemId);

                this.setLoadStatus('load');

                axios.post(process.env.VUE_APP_API_PATH+'/moderator/categories/', formData, {
                    headers: {
                        'Content-Type': 'application/json;charset=utf-8',
                        'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                    },
                    withCredentials: true,
                }).then(response => {
                    if( +response.data.error===401){
                        this.$store.dispatch('auth/logout')
                    } else {
                        self.setLoadStatus('ok');
                        self.getSections();
                        self.$store.commit('moderatorCategories/setItemSaves', true);
                    }

                }).catch(function (error) {
                    self.setLoadStatus('error');
                });
            },
            clearSearchInput: function () {
                this.searchText = '';
                this.sections = this.recursiveSearch(this.sections).SECTIONS;
            },
            searchItems: function () {
                this.isShowClearSearchBtn = true;
                this.sections = this.recursiveSearch(this.sections).SECTIONS;
            },
            recursiveSearch: function (sections) {
                let parentVisible = false;
                for (let index in sections) {
                    let data;

                    if (this.searchText) {
                        sections[index].VISIBLE = (sections[index].NAME.toLowerCase().indexOf(this.searchText.toLowerCase()) === 0);
                    } else {
                        sections[index].VISIBLE = true;
                    }

                    if (!sections[index].VISIBLE) {
                        data = this.recursiveSearch(sections[index].SECTIONS);
                        sections[index].VISIBLE = data.VISIBLE;
                    }

                    if (!parentVisible) {
                        parentVisible = sections[index].VISIBLE;
                    }

                    if (this.searchText) {
                        sections[index].OPENED = sections[index].VISIBLE;
                    } else {
                        sections[index].OPENED = false;
                    }
                }

                return {SECTIONS: sections, VISIBLE: parentVisible};
            },
            saveViewStateSections: function (sections) {
                for (let index in sections) {
                    this.tmpViewStateSections['id' + sections[index].ID] = sections[index].OPENED;


                    if (sections[index].SECTIONS.length) {
                        this.saveViewStateSections(sections[index].SECTIONS);
                    }
                }

            },
            restoreViewStateSections: function (sections) {
                for (let index in sections) {
                    sections[index].OPENED = this.tmpViewStateSections['id' + sections[index].ID];

                    if (sections[index].SECTIONS.length) {
                        sections[index].SECTIONS = this.restoreViewStateSections(sections[index].SECTIONS);
                    }

                }

                return sections;
            },
            changeViewModeSection: function (sections, level, mode) {
                let parentOpened = false,
                    level_show = -1,
                    level_show_all = false;

                if (mode === 'hide_all') {
                    level_show = -1;
                }

                if (mode === 'hide_level2') {
                    level_show = 0;
                }

                if (mode === 'hide_level3') {
                    level_show = 1;
                }

                if (mode === 'show_all') {
                    level_show_all = true;
                }


                for (let index in sections) {
                    let data;


                    if (level_show === level || level_show_all) {
                        sections[index].OPENED = true;
                    } else {
                        sections[index].OPENED = false;
                    }

                    if (sections[index].SECTIONS.length) {
                        data = this.changeViewModeSection(sections[index].SECTIONS, level + 1, mode);

                        if (data.OPENED) {
                            sections[index].OPENED = data.OPENED;
                        } else {
                        }
                    }

                    if (!parentOpened) {
                        parentOpened = sections[index].OPENED;
                    }

                }

                return {SECTIONS: sections, OPENED: parentOpened};
            },
            cancelAddSection: function () {
                this.isAddSection = false;
            },
            createSection: function () {
                if (this.newSectionName) {
                    let tmpId = new Date().getTime(),
                        section = {
                            ID: tmpId,
                            CREATED: true,
                            NAME: this.newSectionName,
                            VISIBLE: true,
                            ACTIVE: 'Y',
                            SECTIONS: [],
                            CHECKED: false
                        };
                    this.sections.unshift(section);
                    this.newSectionName = '';
                    this.isAddSection = false;
                    this.updateSections();
                } else {
                    this.isAddSection = false;
                }
            },
            actionAddSection: function (id) {
                this.isAddSection = !this.isAddSection;
                this.setFocus('addSection');
            },
            changeViewMode: function (mode) {

                this.btnView = {
                    hide_all: false,
                    hide_level2: false,
                    hide_level3: false,
                    restore: false,
                    show_all: false,
                };

                this.btnView[mode] = true;

                if (mode === 'restore') {
                    this.sections = this.restoreViewStateSections(this.sections);
                } else {
                    this.saveViewStateSections(this.sections);
                    this.sections = this.changeViewModeSection(this.sections, 0, mode).SECTIONS;
                }

            }
        }
    };
</script>

