<template>
    <div class="header-category" v-if="pageType && menuList[currentMenuAction]">
        <div class="inner-wrapper">
            <div class="header-category__menu">
                <div class="item" v-for="item in menuList[currentMenuAction]" :key="item.id+currentMenuAction">
                    <router-link :to="'/moderator/'+currentMenuAction+'/'+item.code+'/'" :class="{active:item.code==currentAction && !searchText}">
                        {{item.name}}
                        <span v-if="isCounterShow(item)" class="num" :class="counterColor(item)">!</span>
                    </router-link>

                </div>
                <div class="item">
                    <router-link v-if="currentMenuAction ==='coupons'" :to="'/moderator/coupons/promotion/'" :class="{active:$route.path === '/moderator/coupons/promotion/' && !searchText}">
                        Продвижение
                    </router-link>
                </div>
            </div>
            <!-- end header-category__menu -->
            <providers-search v-if="currentMenuAction == 'providers'"></providers-search>
        </div>
        <!-- end inner-wrapper -->
    </div>
    <!-- end header-category -->
</template>
<script>
    import {mapState} from 'vuex';
    import ProvidersSearch from '@/components/moderator/ProvidersSearch.vue';

    export default {
        name: "ActionMenu",
        props: ['pageType'],
        components: {
            ProvidersSearch
        },
        created() {
            this.$store.dispatch('menus/getActionMenu',{type:'moderator'});
        },
        computed: {
            ...mapState(
            {
                menuList: state => state.menus.itemsAction,
                counters: state => state.moderatorCounters.items,
                searchText: state => state.moderatorProviders.searchText,
            }),
            currentMenuAction(){
                return this.pageType.replace("-default","");        
            },
            defaultStatus(){
                //if(this.currentMenuAction){
                    return this.menuList[this.currentMenuAction][0]['code'];
                //}
            },
            currentAction() {
                let statusId = this.$route.params.action ? this.$route.params.action : this.defaultStatus;

                for (let itemIndex in this.menuList[this.currentMenuAction]) {
                    if (this.menuList[this.currentMenuAction][itemIndex]['code'] == statusId) {
                        return this.menuList[this.currentMenuAction][itemIndex]['code'];
                    }
                }
            }
        },
        methods: {
            isCounterShow: function(item){
                if(this.counters){
                    if(this.$route.params.action === undefined && item.id==this.defaultStatus){
                        return false;    
                    }
                    if(this.counters[this.currentMenuAction] && this.counters[this.currentMenuAction][item.id] && this.$route.params.action != item.id){
                        return true;
                    }
                }
                return false;
            },
            counterColor: function(item){
                if(this.counters){
                    if(this.$route.params.action === undefined && item.id==this.defaultStatus){
                        return false;    
                    }
                    if(this.counters[this.currentMenuAction] && this.counters[this.currentMenuAction][item.id] && this.$route.params.action != item.id){
                        if(this.counters[this.currentMenuAction][item.id]=='orange'){
                            return '--orange';    
                        }
                    }
                }
            }
        }
    }
</script>
