import axios from 'axios'
import {jsonHeader, wwwFormHeader} from '@/helpers/axiosHeaders'
import Vue from 'vue'
import qs from "qs";

const state = {
    items: [],
    listStatus: 0,
    itemDetail: {},
    stat: {},
    detailItems: [],
    isLoad: false,
    isLoadDetail: false,
    isLoadNextPage: true,
    page: 1,
    operations: [],
    backData: {
        scrollTop: 0,
        pageCount: 0
    }
}
const getters = {}
const mutations = {
    setBackData(state, data) {
        Vue.set(state, 'backData', data);
    },
    setNumPage(state, data) {
        state.page = data;
    },
    setItems(state, data) {
        if (typeof data === 'object' && data.items !== undefined) {
            //Vue.set(state,'items', data.items);
            for (let itemIndex in data.items) {
                let item = data.items[itemIndex];
                state.items.push(item);
            }
        }
    },
    setListStatus(state, data) {
        if (typeof data === 'object' && data.listStatus !== undefined) {
            state.listStatus = data.listStatus;
        }
    },
    setItemDetailEmpty(){
        Vue.set(state, 'itemDetail', []);
        Vue.set(state, 'detailItems', []);
    },
    setLoad(state, data) {
        state.isLoad = data;
    },
    setNewPage(state) {
        Vue.set(state, 'items', []);
        state.page = 1;
        state.isLoadNextPage = true;
    },
    incrementPage(state) {
        state.page++;
    },
    isLoadNextPage(state, data) {
        state.isLoadNextPage = data;
    },
    setItemDetail(state, data) {
        if (typeof(state.itemDetail['moderator_action']) === 'undefined' && state.listStatus !== 'work') {
            Vue.set(state,'listStatus', data['moderator_action']);
        }

        Vue.set(state, 'itemDetail', data);
        if(data.templates){
            Vue.set(state, 'itemDetailTemplates', data.templates);
        }
    },
    userBan(state, data) {
        Vue.set(state.itemDetail.user, 'banned', data);
    },
    setStat(state, data) {
        Vue.set(state, 'stat', data);
    },
    setDetailItems(state, data) {
        Vue.set(state, 'detailItems', data.items);
        /*if(data.templates){
            Vue.set(state, 'itemDetailTemplates', data.templates);
        }*/
    },
    setLoadDetail(state, data) {
        state.isLoadDetail = data;
    },
    setOperations(state, data){
        state.operations = data;
    },
    setItemActive(state, data) {
        Vue.set(state.items[data.index], 'itemActiveStat', data.value);
    },
}
const actions = {
    setBackData(context, data) {
        context.commit('setBackData', {
            scrollTop:  window.scrollY,
            pageCount: state.page - 1
        });
    },
    setListStatus(context, data) {
        context.commit('setListStatus', data);
    },
    getItems(context, data) {
        return new Promise((resolve, reject) => {
            context.commit('setItems', {items: []});
            if (!state.isLoad) {
                if (data.reload) {
                    context.commit('setNewPage', {status: data.status});
                }
                if (state.isLoadNextPage) {
                    context.commit('setLoad', true);

                    let params =  {
                        status: data.status,
                        page: state.page,
                        pageCount: state.backData.pageCount
                    };

                    axios.get(process.env.VUE_APP_API_PATH + '/moderator/repayment/', {
                        headers: {
                            'Content-Type': 'application/json;charset=utf-8',
                            'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                        },
                        withCredentials: true,
                        params: params,
                        paramsSerializer: function (params) {
                            return qs.stringify(params, {arrayFormat: 'repeat'})
                        },
                    }).then(response => {
                        context.commit('setLoad', false);
                        if (typeof response.data === 'object' && +response.data.error === 0) {
                            context.commit('setItems', response.data.result);
                            context.commit('isLoadNextPage', response.data.result.isLoadNextPage);
                        } else {
                            if (+response.data.error === 401) {
                                //context.dispatch('auth/logout', {}, {root: true})
                            }
                        }

                        context.commit('incrementPage');
                        resolve();
                    }).catch(function (error) {
                        context.commit('setLoad', false);
                        reject();
                    });
                }
            }
        });
    },
    initDetailPage(context, data) {
        return new Promise((resolve, reject) => {
            if (typeof(state.itemDetail['moderator_action']) !== 'undefined') {
                context.commit('setListStatus', {listStatus: state.itemDetail['moderator_action']});
            }

            context.commit('setDetailItems', {item: {}});
            if (!state.isLoadDetail && data.route.params.id !== undefined) {
                context.commit('setLoadDetail', true);
                axios.get(process.env.VUE_APP_API_PATH + '/moderator/repayment/' + data.route.params.id, {
                    withCredentials: true,
                    headers: {
                        'Content-Type': 'application/json;charset=utf-8',
                        'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                    },
                }).then(response => {
                    if (typeof response.data === 'object' && +response.data.error === 0) {
                        context.commit('setDetailItems', response.data.result);
                        context.commit('setStat', response.data.result.stat);

                        for (let ind in response.data.result.items)
                            if (data.route.params.id === response.data.result.items[ind].id) {
                                context.commit('setItemDetail', response.data.result.items[ind]);
                            }
                        if (response.data.result.provider) {
                            context.commit('setProvider', response.data.result.provider);
                        }
                    } else {
                        if (+response.data.error === 401) {
                            //context.dispatch('auth/logout', {}, {root: true})
                        }
                    }
                    context.commit('setLoadDetail', false);
                    resolve();
                }).catch(function (error) {
                    context.commit('setLoadDetail', false);
                    reject();
                });
            }
        });
    },
    sendForm(context, data){
        return new Promise((resolve, reject) => {
            axios.put(process.env.VUE_APP_API_PATH+'/moderator/repayment/'+data.id+'/',{},{
                headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                withCredentials: true,
                params: {
                    type: data.type,
                    comment: data.comment,
                    days: data.days,
                },
                paramsSerializer: function(params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
            }).then(response => {
                if(typeof response.data === 'object' && +response.data.error === 0) {
                    resolve();
                } else {
                    if( +response.data.error===401){
                        context.dispatch('auth/logout',{},{root:true})
                    }
                }
            }).catch(function (error) {
            });
        });
    },
    sendFormUser(context, data){
        return new Promise((resolve, reject) => {
            axios.put(process.env.VUE_APP_API_PATH+'/moderator/repayment/'+data.id+'/',{},{
                headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                withCredentials: true,
                params: {
                    type: data.type,
                    comment: data.comment,
                    smsUser: data.smsUser,
                },
                paramsSerializer: function(params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
            }).then(response => {
                if(typeof response.data === 'object' && +response.data.error === 0) {
                    resolve();
                } else {
                    if( +response.data.error===401){
                        context.dispatch('auth/logout',{},{root:true})
                    }
                }
            }).catch(function (error) {
            });
        });
    },
    sendLetter(context, data) {
        return new Promise((resolve, reject) => {
            let formData = new FormData();
            formData.append('type', data.type);
            formData.append('sendTo', data.sendTo);
            formData.append('smsText', data.smsText);
            formData.append('emailText', data.emailText);
            formData.append('emailTitle', data.emailTitle);


            axios.post(process.env.VUE_APP_API_PATH+'/moderator/letter/' + data.id + '/', formData, {
                headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                withCredentials: true,
            }).then(response => {
                if (typeof response.data === 'object' && +response.data.error === 0) {
                    resolve();
                } else {
                    if(response.data.message && response.data.message.success === false|| +response.data.error===401){
                        context.dispatch('auth/logout',{},{root:true})
                    }
                }
            }).catch(function (error) {
            });
        });
    },
}
export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}