<template>
    <div>
        <div class="popup__header popup__header--dotted --bg-orange">
            <div class="title">Уведомить всех поставщиков</div>
            
            <div class="subtitle --icon">
                <span class="i i-warning i--white"></span>
                <span class="t text-size-m text-bold text-color-white">Это массовая рассылка, поэтому формулируйте текст так, <br>
                чтобы он подходил для любого получателя</span>
            </div>
        </div>


        <div class="popup__content">

            <div class="h2 mb16">Получатели</div>

            <div class="mb8">
                <input @change="removeError('statusesError')" :class="{error:form.statusesError}" id="group-provider-send-newProviders" v-model="form.statuses.newProviders" type="checkbox">
                <label class="text-size-s text-color-grey text-bold mr24" for="group-provider-send-newProviders">НОВЫЕ</label>

                <input @change="removeError('statusesError')" :class="{error:form.statusesError}" id="group-provider-send-moderation" v-model="form.statuses.moderation" type="checkbox">
                <label  class="text-size-s text-color-grey text-bold mr24" for="group-provider-send-moderation">НА ПРОВЕРКЕ</label>

                <input @change="removeError('statusesError')" :class="{error:form.statusesError}" id="group-provider-send-wait_contract" v-model="form.statuses.wait_contract" type="checkbox">
                <label  class="text-size-s text-color-grey text-bold mr24" for="group-provider-send-wait_contract">ЖДУТ ДОГОВОР</label>

                <input @change="removeError('statusesError')" :class="{error:form.statusesError}" id="group-provider-send-wait" v-model="form.statuses.wait" type="checkbox">
                <label  class="text-size-s text-color-grey text-bold mr24" for="group-provider-send-wait">ЖДЁМ ДОКУМЕНТЫ</label>

                <input @change="removeError('statusesError')" :class="{error:form.statusesError}" id="group-provider-send-verify" v-model="form.statuses.verify" type="checkbox">
                <label  class="text-size-s text-color-grey text-bold" for="group-provider-send-verify">АКТИВНЫЕ</label>
            </div>
            <div class="h2 mb16">СМС</div>

            <textarea-field :placeholder="'Текст СМС'" :hasCounterSMS="true" @focus="removeError('smsTextError')" :is-error="form.smsTextError" v-model="form.smsText" :add-style="{minHeight: 80 + 'px', 'height': 'auto'}"></textarea-field>

            <div class="h2 mb16">Письмо</div>

            <div class="mb8">
                <input :class="{error:form.emailTitleError}" @focus="removeError('emailTitleError')" :placeholder="'Тема письма'" type="text" class="inp size-s full" v-model="form.emailTitle">
            </div>

            <text-editor-min v-model="form.emailText" @click.native="removeError('emailTextError')" :error="form.emailTextError" :hide-toolbar="true" :placeholder="'Текст письма'" :value-default="form.emailText" />

            <div class="mt32">
                <div class="mt16">
                    <input id="i-understand-modal" @change="removeError('understandError')" v-model="form.understand" :class="{error:form.understandError}" type="checkbox">
                    <label for="i-understand-modal">Я понимаю, что текст будет разослан всем поставщикам</label>
                </div>
            </div>
            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <submit-btn class="btn--orange" @click.native="send" :type="form.btnType">Подтвердить</submit-btn>
                </div>
                
                <div class="row-b__col elem-pos-fit">
                    <div class="dif">
                        <span class="i i-warning i--orange mr4 mt-2"></span>
                        <span class="t text-color-orange text-size-s">Это массовая рассылка, поэтому формулируйте текст так, <br>
                        чтобы он подходил для любого получателя</span>
                    </div>
                </div>
            </div>

        </div>
    </div>
</template>
<script>
    import {mapState} from 'vuex'
    import TextareaField from '@/components/forms/TextareaField.vue'
    import TextEditorMin from '@/components/editor/TextEditorMin.vue'
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'

    export default {
        name: "SendAllProviders",
        components: {
            TextareaField,
            TextEditorMin,
            SubmitBtn
        },
        methods: {
            validation: function() {
                let isSuccess = true;
                this.form.understandError = false;
                this.form.statusesError = false;
                this.form.smsTextError = false;
                this.form.emailTextError = false;
                this.form.emailTitleError = false;

                if (this.form.understand === false) {
                    isSuccess = false;
                    setTimeout(()=>{
                        this.form.understandError = true;
                    },150);

                }
                let emptyStatuses = true;
                for(let status in this.form.statuses){
                    if(this.form.statuses[status]){
                        emptyStatuses = false
                    }
                }
                if(emptyStatuses){
                    setTimeout(()=>{
                        this.form.statusesError = true;
                    },150);

                    isSuccess = false;
                }

                if (this.form.smsText.length < 10 && this.form.emailText.length < 10) {
                    isSuccess = false;
                    setTimeout(()=>{
                        this.form.smsTextError = true;
                        this.form.emailTextError = true;
                    },150);

                } else {
                    this.form.smsTextError = false;
                    this.form.emailTextError = false;
                    this.form.emailTitleError = false;
                    if (this.form.smsText.length < 10 && this.form.smsText.length > 0) {
                        isSuccess = false;
                        setTimeout(()=>{
                            this.form.smsTextError = true;
                        },150);

                    }
                    if (this.form.emailText.length < 10 && this.form.emailText.length > 0) {
                        isSuccess = false;
                        setTimeout(()=>{
                            this.form.emailTextError = true;
                        },150);

                    }
                }

                if(this.form.emailTitle.length < 3 && (this.form.emailText.length > 0 || this.form.smsText.length < 10 && this.form.emailText.length < 10)){
                    isSuccess = false;
                    setTimeout(()=>{
                        this.form.emailTitleError = true;
                    },150);

                }

                return isSuccess;
            },
            send: function() {
                if (this.validation()) {
                    if (!this.form.formLoad) {
                        this.form.formLoad = true;
                        this.form.commentError = false;
                        this.form.btnType = 'load';
                        this.$store.dispatch('moderatorProviders/sendForm', {
                            type: 'send_all',
                            smsText: this.form.smsText,
                            emailTitle: this.form.emailTitle,
                            emailText: this.form.emailText,
                            statuses: this.form.statuses,
                        }).then(() => {
                            this.form.formLoad = false;
                                this.form.btnType = 'ok';
                            setTimeout(() => {
                                this.$store.commit('popups/close', {name: this.popups.sendAllProviders.name});
                                this.$store.dispatch('moderatorProviders/getItem', {id: this.item.id}).then((e)=>{
                                    this.$store.dispatch('moderatorProviders/setListStatus', {
                                        listStatus: e.status
                                    });
                                });
                            }, 300);
                        });
                    }
                } else {

                }
            },
            removeError(field) {
                this.form[field] = false;
            }
        },
        data: function(){
            return {
                form:{
                    smsText: '',
                    smsTextError: false,
                    emailTitle: '',
                    emailTitleError: false,
                    emailText: '',
                    emailTextError: false,
                    understand: false,
                    understandError: false,
                    statusesError: false,
                    formLoad: false,
                    btnType: 'normal',
                    statuses:{
                        newProviders: false,
                        moderation: false,
                        wait_contract: false,
                        wait: false,
                        verify: false,
                    }
                }
            }
        },
        mounted() {

        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        }
    }
</script>
<style scoped>

