<template>
    <div>
        <div class="popup__header popup__header--dotted --bg-green">
            <div class="title">Разблокировать покупателя</div>
            <div class="subtitle">{{detail.user.fio}}</div>
        </div>


        <div class="popup__content">

            <div class="h2 mb16">Служебный комментарий</div>

            <textarea-field @focus="removeError('commentError')"
                            v-model="form.comment"
                            :is-error="form.commentError"
                            :placeholder="'Прокомментируйте своё решение'"
                            :add-style="{minHeight: 88 + 'px'}"></textarea-field>

            <div class="h2 mb16">СМС покупателю на {{detail.user.phone }}</div>

            <textarea-field :hasCounterSMS="true"
                            @focus="removeError('smsUserError')"
                            :is-error="form.smsUserError"
                            :placeholder="'Например, «Вы разблокированы на проекте ZemSkidki.ru»'"
                            v-model="form.smsUser"></textarea-field>



            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <submit-btn class="btn--green" @click.native="send" :type="form.btnType">Подтвердить</submit-btn>
                </div>


                <div class="row-b__col elem-pos-fit">
                    <div class="text-color-grey">Для разблокировки покупателя нужна веская причина. <br>
                        Ну, или хорошее настроение...</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import TextareaField from '@/components/forms/TextareaField.vue'
    import TextEditorMin from '@/components/editor/TextEditorMin.vue'
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'

    export default {
        name: "UnBanUser",
        components: {
            TextareaField,
            TextEditorMin,
            SubmitBtn
        },
        data: function(){
            return {
                form:{
                    comment: '',
                    commentError: false,
                    smsUser: '',
                    smsUserError: false,
                    formLoad: false,
                    btnType: 'normal'
                }
            }
        },
        mounted() {

        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                item: state => state.moderatorProviders.itemDetail,
                detail: state => state.moderatorRepayment.itemDetail,
            }),
        },
        methods: {
            validation: function() {
                let isSuccess = true;
                this.form.commentError = false;
                if (this.form.comment.length < 10) {
                    isSuccess = false;
                    setTimeout(()=>{
                        this.form.commentError = true;
                    },150);
                }

                return isSuccess;
            },
            send: function () {
                if (this.validation()) {
                    if (!this.formLoad) {
                        this.formLoad = true;
                        this.form.commentError = false;
                        this.form.btnType = 'load';
                        this.$store.dispatch('moderatorRepayment/sendFormUser', {
                            type: 'unBanUser',
                            id: this.detail.user.id,
                            comment: this.form.comment,
                            smsUser: this.form.smsUser,
                        }).then(() => {
                            this.form.formLoad = false,
                                this.form.btnType = 'ok';
                            setTimeout(() => {
                                this.$store.commit('popups/close', {name: this.popups.userUnBan.name});
                                this.$store.commit('moderatorRepayment/userBan', false);
                            }, 300);
                        });
                    }
                }
            },
            removeError(field) {
                this.form[field] = false;
            }
        },
    }
</script>

