import axios from 'axios'
import {jsonHeader, wwwFormHeader} from '@/helpers/axiosHeaders'
import Vue from 'vue'
import qs from "qs";

const state = {
    graph: [],
    months: [],
    lastMonth: {},
    maxProfit: 0,
    startUpdate: false,
    total: {},
    regionsRating: {
        items: [],
        isLoadedProcess: true,
        isLastPage: false,
        sort: {
            current: {
                order: 'profit',
                by: 'desc',
            },
            order: {
                name: {
                    code: 'name',
                },
                action: {
                    code: 'action',
                },
                provider: {
                    code: 'provider',
                },
                view: {
                    code: 'view',
                },
                repay: {
                    code: 'repay',
                },
                coupons: {
                    code: 'coupons',
                },
                profit: {
                    code: 'profit',
                },
            },
            by: {
                asc: 'asc',
                desc: 'desc',
            }
        },
        page: 1,
    },
    cityRating: {
        items: [],
        isLoadedProcess: true,
        isLastPage: false,
        sort: {
            current: {
                order: 'profit',
                by: 'desc',
            },
            order: {
                name: {
                    code: 'name',
                },
                action: {
                    code: 'action',
                },
                provider: {
                    code: 'provider',
                },
                view: {
                    code: 'view',
                },
                repay: {
                    code: 'repay',
                },
                coupons: {
                    code: 'coupons',
                },
                profit: {
                    code: 'profit',
                },
            },
            by: {
                asc: 'asc',
                desc: 'desc',
            }
        },
        page: 1,
    }
}
const getters = {}
const mutations = {
    setItems(state, data) {
        Vue.set(state,'months', data.months);
        Vue.set(state,'maxProfit', data.maxProfit);
        Vue.set(state,'total', data.total);
        let incomes = [...data.incomes]
        let lastMonth = incomes.pop();
        Vue.set(state,'graph', incomes);
        Vue.set(state,'lastMonth', lastMonth);
    },
    setStartUpdate(state, data) {
        Vue.set(state,'startUpdate', data);
    },
    setSortCityRating(state, data) {
        if(data.order !== state.cityRating.sort.current.order){
            Vue.set(state.cityRating.sort.current, 'order', data.order);
            if(data.order === 'name'){
                Vue.set(state.cityRating.sort.current, 'by', 'asc');
            } else {
                Vue.set(state.cityRating.sort.current, 'by', 'desc');
            }
        } else {
            if(state.cityRating.sort.current.by === 'asc'){
                Vue.set(state.cityRating.sort.current, 'by', 'desc');
            } else {
                Vue.set(state.cityRating.sort.current, 'by', 'asc');
            }

        }
    },
    incrementPageCityRating(state) {
        Vue.set(state.cityRating, 'page', state.cityRating.page + 1);
    },
    resetPageCityRating(state) {
        Vue.set(state.cityRating, 'page', 1);
        Vue.set(state.cityRating, 'items', []);
        Vue.set(state.cityRating, 'isLastPage', false);
    },
    setItemsCityRating(state, data) {
        for (let ind in data.rating.cities) {
            if (data.rating.cities.hasOwnProperty(ind)) {
                state.cityRating.items.push(data.rating.cities[ind]);
            }
        }
    },
    setLoadedProcessCityRating(state, bool) {
        Vue.set(state.cityRating, 'isLoadedProcess', bool);
    },
    setLastPageCityRating(state, bool) {
        Vue.set(state.cityRating, 'isLastPage', bool);
    },
    setSortRegionsRating(state, data) {
        if(data.order !== state.regionsRating.sort.current.order){
            Vue.set(state.regionsRating.sort.current, 'order', data.order);
            if(data.order === 'name'){
                Vue.set(state.regionsRating.sort.current, 'by', 'asc');
            } else {
                Vue.set(state.regionsRating.sort.current, 'by', 'desc');
            }
        } else {
            if(state.regionsRating.sort.current.by === 'asc'){
                Vue.set(state.regionsRating.sort.current, 'by', 'desc');
            } else {
                Vue.set(state.regionsRating.sort.current, 'by', 'asc');
            }

        }
    },
    incrementPageRegionsRating(state) {
        Vue.set(state.regionsRating, 'page', state.regionsRating.page + 1);
    },
    resetPageRegionsRating(state) {
        Vue.set(state.regionsRating, 'page', 1);
        Vue.set(state.regionsRating, 'items', []);
        Vue.set(state.regionsRating, 'isLastPage', false);
        console.log('resetPageRegionsRating');
    },
    setItemsRegionsRating(state, data) {
        for (let ind in data.rating.cities) {
            if (data.rating.cities.hasOwnProperty(ind)) {
                state.regionsRating.items.push(data.rating.cities[ind]);
            }
        }
    },
    setLoadedProcessRegionsRating(state, bool) {
        Vue.set(state.regionsRating, 'isLoadedProcess', bool);
    },
    setLastPageRegionsRating(state, bool) {
        Vue.set(state.regionsRating, 'isLastPage', bool);
    },
}
const actions = {
    getCityRating(context, data){
        if (state.cityRating.isLoadedProcess) {

            if (data.action === 'sort') {
                context.commit('resetPageCityRating');
                context.commit('setLastPageCityRating', false);
            }

            if (!state.cityRating.isLastPage) {

                let query = {
                    sort: state.cityRating.sort.current,
                    page: state.cityRating.page,
                    type: 'cities',
                };

                context.commit('setLoadedProcessCityRating', false);

                axios.get(process.env.VUE_APP_API_PATH+'/moderator/city_rating/', {
                    headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                    params: query,
                    paramsSerializer: function(params) {
                        return qs.stringify(params, {arrayFormat: 'repeat'})
                    },
                    withCredentials: true,
                }).then(response => {
                    if (typeof response.data === 'object' && +response.data.error === 0) {
                        context.commit('setItemsCityRating', response.data.result);
                        context.commit('incrementPageCityRating');

                        if (response.data.result.rating.isLastPage) {
                            context.commit('setLastPageCityRating', true);
                        }
                    }

                    context.commit('setLoadedProcessCityRating', true);
                }).catch(function (error) {
                    context.commit('setLoadedProcessCityRating', true);
                });
            }
        }
    },
    getRegionsRating(context, data){
        if (state.regionsRating.isLoadedProcess) {

            if (data.action === 'sort') {
                context.commit('resetPageRegionsRating');
                context.commit('setLastPageRegionsRating', false);
            }

            if (!state.regionsRating.isLastPage) {

                let query = {
                    sort: state.regionsRating.sort.current,
                    page: state.regionsRating.page,
                    type: 'area',
                };

                context.commit('setLoadedProcessRegionsRating', false);

                axios.get(process.env.VUE_APP_API_PATH+'/moderator/city_rating/', {
                    headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                    params: query,
                    paramsSerializer: function(params) {
                        return qs.stringify(params, {arrayFormat: 'repeat'})
                    },
                    withCredentials: true,
                }).then(response => {
                    if (typeof response.data === 'object' && +response.data.error === 0) {
                        context.commit('setItemsRegionsRating', response.data.result);
                        context.commit('incrementPageRegionsRating');

                        if (response.data.result.rating.isLastPage) {
                            context.commit('setLastPageRegionsRating', true);
                        }
                    }

                    context.commit('setLoadedProcessRegionsRating', true);
                }).catch(function (error) {
                    context.commit('setLoadedProcessRegionsRating', true);
                });
            }
        }
    },
    postStartUpdate(context, data){
        context.commit('setStartUpdate', true);
        axios.post(process.env.VUE_APP_API_PATH+'/moderator/statistic/',{} ,{
            headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
            withCredentials: true,
        }).then(response => {
            if (typeof response.data === 'object' && +response.data.error === 0) {

            } else {
                if( +response.data.error===401){
                    context.dispatch('auth/logout',{},{root:true})
                }
            }
        }).catch(function (error) {

        });
    },
    checkUpdate(context, data) {
        return new Promise((resolve, reject) => {
        axios.get(process.env.VUE_APP_API_PATH+'/moderator/statistic/', {
            headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
            withCredentials: true,
            params: {
                type: 'checkUpdate',
            },
            paramsSerializer: function(params) {
                return qs.stringify(params, {arrayFormat: 'repeat'})
            },
        }).then(response => {
            if (typeof response.data === 'object' && +response.data.error === 0) {
                if(response.data.result.status === false){
                    context.commit('setStartUpdate', false);
                }
            } else {
                if( +response.data.error===401){
                    context.dispatch('auth/logout',{},{root:true})
                }
            }
            resolve(response.data.result.status);
        }).catch(function (error) {
            reject();
        });
        });
    },
    getItems(context, data) {
        axios.get(process.env.VUE_APP_API_PATH+'/moderator/statistic/', {
            headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
            withCredentials: true,
        }).then(response => {
            if (typeof response.data === 'object' && +response.data.error === 0) {
                context.commit('setItems', response.data.result.statistic);
            } else {
                if( +response.data.error===401){
                    context.dispatch('auth/logout',{},{root:true})
                }
            }
        }).catch(function (error) {

        });
    },
}
export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}