<template>
    <div class="all-wrapper grey-wrapper" id="app" v-show="isUserLoad" :class="{'no-footer': this.$route.name == 'cooperation-edit' || 'repayment-detail' || 'coupons-about-edit' || 'coupons-edit' || 'provider-edit' || 'providers-about-edit' || 'partner-about-edit' || 'coupons-detail'}">
        <header :class="{'header': isActionEditPage || isCooperationEdit || isProviderEdit ||isProviderConstruct || isCouponDetail || isRepaymentDetail, 'header-moderator --moderator': !isActionEditPage && !isRepaymentDetail && !isCooperationEdit && !isProviderEdit && !isProviderConstruct && !isCouponDetail, 'header--black': isActionEditPage || isCooperationEdit || isProviderConstruct ||isProviderEdit || isCouponDetail || isRepaymentDetail, '--auth': !isModerator}">
            <div class="header-main" :class="{'header-main--editor':  isActionEditPage || isCooperationEdit || isProviderConstruct ||isProviderEdit || isCouponDetail || isRepaymentDetail}">
                <div class="inner-wrapper">
                    <div class="h-left">
                        <div class="h-logo" v-if="isShowMenu">
                            <router-link class="h-logo-link" to="/moderator/">
                                <span class="icon"></span>
                                <span class="t">moderator</span>
                            </router-link>

                            <tooltip ref="tooltipNotifyReset"
                             :direction="'bottom-center-direction'"
                             :add-class-wrap="'h-logo__tooltip'"
                             :isBtnClose="false"
                             :add-style="{width: 175 + 'px'}">
                                <span slot="tooltipBtn"></span>

                                <div slot="tooltipContent">
                                    <div class="text-center">
                                        <div>
                                            <a href="" class="i i-refresh2 i--blue"></a>
                                        </div>
                                        <div class="text-size-s" style="margin-top: 4px; ">
                                            Мы обновили сайт.<br>
                                            Чтобы все заработало –<br>
                                            <a href="">перезагрузите</a> страницу
                                        </div>
                                    </div>
                                </div>
                            </tooltip>
                        </div>

                        <div class="h-nav-wrap" v-if="isShowMenu && isModerator">
                            <top-menu :type="'moderator'"></top-menu>
                        </div>

                        <div v-if="(isCooperationEdit && isCooperationEditLoaded) && successModerator" class="h-editor-back">
                            <router-link v-if="cooperationBackName"
                                         :to="'/moderator/cooperation/'+cooperationBackCode+'/'"
                                         class="h-editor-link">
                                <span class="i i-back"></span>
                                <span class="t">{{cooperationBackName}}</span>
                            </router-link>
                        </div>

                        <div v-if="(isProviderEdit && isProviderEditLoaded) && successModerator" class="h-editor-back">
                            <router-link :to="'/moderator/providers/'+providerBackCode+'/'" class="h-editor-link">
                                <span class="i i-back"></span>
                                <span class="t">{{providerBackName}}</span>
                            </router-link>
                        </div>

                        <div v-if="isCouponDetail && !couponLoading && actionBackName!='' && successModerator" class="h-editor-back">
                            <router-link :to="'/moderator/coupons/' + actionBackCode + '/'" class="h-editor-link">
                                <span class="i i-back"></span>
                                <span class="t">{{actionBackName}}</span>
                            </router-link>
                        </div>

                        <div v-if="isCouponDetail && !couponLoading && !action.already_moderated && Object.keys(action).length > 0 && successModerator" class="h-editor-text">
                            <span class="i i-warning i--red"></span>
                            <span class="t text-color-red">Документы не получены</span>
                        </div>

                        <div v-if="isCouponDetail && !couponLoading && action.status == 17 && successModerator" class="h-editor-text">
                            <span class="i i-warning i--red"></span>
                            <span class="t text-color-red">Акция в разработке у партнёра</span>
                        </div>

                        <div v-if="(isRepaymentDetail && isRepaymentDetailLoaded) && successModerator" class="h-editor-back">
                            <router-link :to="'/moderator/repayment/' + repaymentBackCode + '/'" class="h-editor-link">
                                <span class="i i-back"></span>
                                <span class="t">{{repaymentBackName}}</span>
                            </router-link>
                        </div>


                        <!-- end header-category__menu -->
                        <div v-if="isProviderEdit && successModerator" class="h-editor-text" style="opacity: 0.3">
                            <span v-if="providerDescr" class="i i-info"></span>
                            <span class="t" v-html="providerDescr"></span>
                        </div>

                       <!-- <div v-if="(isRepaymentDetail && isRepaymentDetailLoaded)" class="h-editor-text" style="opacity: 0.3">
                            <span class="i i-info"></span>
                            <span class="t">Все купоны покупателя</span>
                        </div>-->

                    </div>
                    <!-- end h-left -->



                    <div class="h-right">
                        <div class="h-right__editor"
                             v-if="isCooperationEdit && isCooperationEditLoaded && (cooperationItem.isReview || cooperationItem.isDelayed) && successModerator">
                            <span @click="openRequestPause" v-if="cooperationItem.isReview" class="btn h-editor-btn btn--ico">
                                <span class="i i-pause2"></span>
                                <span class="t">Отложить</span>
                            </span>

                            <span @click="openRequestReject" class="btn h-editor-btn btn--ico">
                                <span class="i i-close2"></span>
                                <span class="t">Отклонить</span>
                            </span>
                            <span @click="openRequestApprove" class="btn h-editor-btn btn--ico">
                                <span class="i i-ok2"></span>
                                <span class="t">Одобрить</span>
                            </span>
                        </div>


                        <div class="h-right__editor"
                             v-if="isProviderEdit && isProviderEditLoaded && successModerator">
                            <span @click="openProviderPopup('overtime')" v-if="providerOperations.overtime" class="btn h-editor-btn btn--ico">
                                <span class="i i-clock2"></span>
                                <span class="t">Дать время</span>
                            </span>

                            <span @click="openProviderPopup('reject')" v-if="providerOperations.reject" class="btn h-editor-btn btn--ico">
                                <span class="i i-close2"></span>
                                <span class="t">Отклонить</span>
                            </span>

                            <div v-if="checkInnOgrn && providerOperations.approve">
                                <span @click="openProviderPopup('approve')" v-if="providerOperations.approve" class="btn h-editor-btn btn--ico">
                                    <span class="i i-ok2"></span>
                                    <span class="t">Предварительно одобрить</span>
                                </span>
                            </div>
                            <div v-else-if="providerOperations.approve">
                                <span @click="openPrintContractError" v-if="providerOperations.approve" class="btn h-editor-btn btn--ico">
                                    <span class="i i-ok2"></span>
                                    <span class="t">Предварительно одобрить</span>
                                </span>
                            </div>

                            <a :href="providerContractLink" target="_blank" v-if="providerContractLink && providerOperations.print" class="btn h-editor-btn btn--ico">
                                <span class="i i-print" style="-webkit-filter: brightness(0) invert(1);
    filter: brightness(0) invert(1);"></span>
                                <span class="t">Распечатать договор</span>
                            </a>

                            <span @click="openProviderPopup('check_send')" v-if="providerOperations.check_send" class="btn h-editor-btn btn--ico">
                                <span class="i i-ok2"></span>
                                <span class="t">Отметить отправку</span>
                            </span>

                            <span @click="openProviderPopup('final_approve')" v-if="providerOperations.final_approve" class="btn h-editor-btn btn--ico">
                                <span class="i i-ok2"></span>
                                <span class="t">Окончательно одобрить</span>
                            </span>

                            <span @click="openProviderPopup('block')" v-if="providerOperations.block" class="btn h-editor-btn btn--ico">
                                <span class="i i-stop"></span>
                                <span class="t">Заблокировать</span>
                            </span>

                            <span @click="openProviderPopup('cancel_reject')" v-if="providerOperations.cancel_reject" class="btn h-editor-btn btn--ico">
                                <span class="i i-pointer4"></span>
                                <span class="t">Отменить отклонение</span>
                            </span>

                            <span @click="openProviderPopup('cancel_block')" v-if="providerOperations.cancel_block" class="btn h-editor-btn btn--ico">
                                <span class="i i-lock2"></span>
                                <span class="t">Разблокировать</span>
                            </span>
                        </div>


                        <div class="h-right__editor"
                            v-if="isCouponDetail && isCouponOperationsLoaded && successModerator">
                            <template v-if="action.already_moderated">
                                <span @click="routeCouponEdit($route.params.id)" v-if="operations.edit" class="btn h-editor-btn btn--ico">
                                    <span class="i i-pencil"></span>
                                    <span class="t">Редактировать</span>
                                </span>

                                <span @click="openCategoryChoice" v-if="operations.catalog_binding" class="btn h-editor-btn btn--ico">
                                    <span class="i i-book2"></span>
                                    <span class="t" v-if="action.statusModerator==status.new">Привязать к каталогу</span>
                                    <span class="t" v-else>Изменить привязку к каталогу</span>
                                </span>

                                <span @click="openCouponReject" v-if="operations.reject" class="btn h-editor-btn btn--ico">
                                    <span class="i i-close2"></span>
                                    <span class="t">Отклонить</span>
                                </span>

                                <span @click="openCouponBlock" v-if="operations.block" class="btn h-editor-btn btn--ico">
                                    <span class="i i-stop"></span>
                                    <span class="t">Заблокировать</span>
                                </span>

                                <span @click="openCouponUnBlock" v-if="operations.unblock" class="btn h-editor-btn btn--ico">
                                    <span class="i i-lock2"></span>
                                    <span class="t">Разблокировать</span>
                                </span>

                                <span @click="openCouponApprove" v-if="operations.approve" class="btn h-editor-btn btn--ico btn--green">
                                    <span class="i i-ok3"></span>
                                    <span class="t">Опубликовать</span>
                                </span>
                                <submit-btn tag="span" v-if="operations.rejectEdit"
                                        class="btn h-editor-btn btn--ico"
                                        size="small"
                                        @click.native="rejectEdit"
                                        :type="buttons.rejectEdit.state">
                                    <span class="i i-close2"></span>
                                    <span class="t">Отклонить</span>
                                </submit-btn>

                                <submit-btn tag="span" v-if="operations.approveEdit"
                                        class="btn h-editor-btn btn--ico btn--green"
                                        size="small"
                                        @click.native="approveEdit"
                                        :type="buttons.approveEdit.state">
                                    <span class="i i-ok3"></span>
                                    <span class="t">Опубликовать изменения</span>
                                </submit-btn>
                            </template>

                            <template v-else>
                                <span @click="openCouponReject" v-if="operations.reject" class="btn h-editor-btn btn--ico">
                                    <span class="i i-close2"></span>
                                    <span class="t">Отклонить</span>
                                </span>
                            </template>
                        </div>
                        <div class="h-right__editor"
                            v-if="isRepaymentDetail && isRepaymentDetailLoaded && false">
                                <span @click="openCouponCheating" v-if="repaymentOperations.cheating" class="btn h-editor-btn btn--ico">
                                    <span class="i i-warning"></span>
                                    <span class="t">Нас кинули</span>
                                </span>
                                <span @click="openCouponExtend" v-if="repaymentOperations.extend" class="btn h-editor-btn btn--ico">
                                    <span class="i i-clock2"></span>
                                    <span class="t">Продлить</span>
                                </span>

                                <span @click="openCouponAnnul" v-if="repaymentOperations.annul" class="btn h-editor-btn btn--ico">
                                    <span class="i i i-close2"></span>
                                    <span class="t">Аннулировать</span>
                                </span>

                        </div>
                        <div class="h-right__editor" v-if="(isRepaymentDetail && isRepaymentDetailLoaded && repayment.user) && successModerator">
                            <div  @click="openUserUnBan" class="btn h-editor-btn btn--ico" v-if="repayment.user.banned">
                                <span class="i i-lock2"></span>
                                <span class="t">Разблокировать покупателя</span>
                            </div>
                            <div  @click="openUserBan" class="btn h-editor-btn btn--ico" v-else>
                                <span class="i i-stop"></span>
                                <span class="t">Заблокировать покупателя</span>
                            </div>
                        </div>

                        <div class="h-right__editor"
                             v-if="isProviderConstruct && successModerator">
                            <router-link :to="cancelProviderAboutEdit" class="h-editor-link">Отмена</router-link>
                            <submit-btn tag="span"
                                        class="btn h-editor-btn btn--ico btn--green"
                                        @click.native="saveAboutForm"
                                        size="small"
                                        :type="buttons.savePartner.state">
                                <span class="i i-ok2" v-if="buttons.savePartner.state === 'normal'"></span>
                                <span class="t">Сохранить</span>
                            </submit-btn>
                        </div>


                        <div class="h-right__editor"
                            v-if="isActionEditPage && successModerator">
                            <router-link v-if="isActionEditPage"
                                        :to="'/moderator/coupons/'+this.actionBackCode+'/id'+$route.params.id+'/'"
                                         class="link-white-trans">
                                Отмена
                            </router-link>

                            <submit-btn tag="span" v-if="isActionEditPage"
                                        class="btn h-editor-btn btn--ico btn--green"
                                        size="small"
                                        @click.native="saveAction"
                                        :type="buttons.saveAction.state">
                                <span class="i i-ok2" v-if="buttons.saveAction.state === 'normal'"></span>
                                <span class="t">Сохранить</span>
                            </submit-btn>
                        </div>

                        <a href="#" v-if="isShowMenu && token && showMenuRightIcon" @click.prevent="openModalSendAllProviders" class="h-link h-link--icon dif">
                            <span class="i i-megafon"></span>
                        </a>

                        <a href="#" v-if="isShowMenu && token && showMenuRightIcon" @click.prevent="openCategoryChoice" class="h-link h-link--icon dif">
                            <span class="i i-book2"></span>
                        </a>

                        <div v-if="isShowMenu">
                            <tooltip v-if="user.firstName"
                                     :direction="'bottom-right-direction'"
                                     :add-class-wrap="'h-user'"
                                     :add-class-btn="'l'"
                                     :add-class="'h-user-menu'"
                                     :is-padding="false">
                                <span slot="tooltipBtn">
                                    <span class="t">
                                        {{ user.firstName.charAt(0) }}
                                    </span>
                                </span>

                                <div slot="tooltipContent">
                                    <div class="h-user-menu__wrap">
                                        <div class="h-user-menu__item">
                                            <span class="icon">
                                                <span class="i i-man2"></span>
                                            </span>
                                            <span class="t">{{ user.lastName }} {{ user.firstName }} {{ user.secondName }}</span>
                                        </div>

                                        <div class="h-user-menu__item">
                                            <span class="icon">
                                                <span class="i i-phone"></span>
                                            </span>
                                            <span class="t">{{ user.phone }}</span>
                                        </div>

                                        <div class="h-user-menu__item">
                                            <span class="icon">
                                                <span class="i i-mail"></span>
                                            </span>
                                            <span class="t">{{ user.email }}</span>
                                        </div>

                                        <div class="h-user-menu__item">
                                            <span class="icon">
                                                <span class="i i-marker"></span>
                                            </span>
                                            <span class="t">{{ currentCityIp.name }}</span>
                                        </div>

                                    </div>

                                    <div class="h-user-menu__bottom">
                                        <a href="javascript:void(0);" class="link" @click="logout()">
                                            <span class="i i-exit2"></span>
                                            <span class="t">ВЫЙТИ</span>
                                        </a>
                                    </div>
                                </div>
                            </tooltip>
                        </div>
                    </div>
                </div>
                <!-- end inner-wrapper -->
            </div>
            <!-- end header__bottom -->
            <action-menu v-if="isModerator && successModerator" :pageType="pageRoute"></action-menu>
            <div class="header-category" v-if="pageRoute === 'moderator-statistic' || pageRoute === 'cities-rating'">
                <div class="inner-wrapper">
                    <div class="header-category__menu">
                        <div class="item">
                            <router-link :class="{active:pageRoute === 'moderator-statistic'}" :to="'/moderator/statistic/'">Общая</router-link>
                        </div>
                        <div class="item">
                            <router-link :class="{active:pageRoute === 'cities-rating'}" :to="'/moderator/statistic/cities/'">регионы</router-link>
                        </div>
                    </div>
                </div>
                <!-- end inner-wrapper -->
            </div>
        </header>


        <main id="main">
            <router-view v-if="isModerator && successModerator"/>
            <section v-else>
                <div class="content">
                    <div class="main-title">
                        <div class="main-title__wrap main-title--nowrap">
                            <div class="h1">Личный кабинет Модератора</div>
                        </div>
                        <div class="main-title__right"></div>
                    </div>


                    <section class="container">
                        <auth v-if="!token && !isBanned && isValidPhone"></auth>

                        <div class="wrapper-content" v-else-if="isBanned">
                            <div class="sect-blank">
                                <div class="wrap text-center">
                                    <div class="text-size-l text-bolder text-color-red mt16">Слишком много попыток входа!</div>
                                    <div class="mt">Вы заблокированы на 12 часов.</div>
                                    <div class="mt">Если у вас что-то срочное, <a href="javascript:void(0);" @click="openMailZems">напишите</a> руководителю проекта</div>
                                </div>

                                <div class="mini-zems"></div>
                            </div>
                            <!-- end sect-blank -->
                        </div>


                        <div class="wrapper-content" v-else>
                            <div class="sect-blank">
                                <div class="wrap text-center">
                                    <div class="text-size-l text-bolder text-color-red mt16">
                                        У номера {{ user.phone ? user.phone : formAuthFields.login.value }}<br>
                                        нет доступа в этот раздел
                                    </div>
                                </div>
                            </div>
                            <!-- end sect-blank -->
                        </div>
                    </section>
                </div>
            </section>
        </main>

        <vue-footer :hide-offer="true"></vue-footer>
        <popup :show="popups.citySelect.show"
               :width="456"
               :add-class="'popup-city'"
               :name="popups.citySelect.name">
            <city-select :is-show-all="false"></city-select>
        </popup>


        <popup :show="popups.publicAuth.show"
               :is-set-blank="isBanned"
               :is-resize="token && !userCheck && !isBanned"
               :name="popups.publicAuth.name">
            <auth v-if="!token && !isBanned"></auth>
            <registration v-if="token && !userCheck && !isBanned"></registration>
            <many-auth v-if="isBanned"></many-auth>
        </popup>

        <popup :show="popups.sendAllProviders.show"
               :is-set-styled="true"
               :name="popups.sendAllProviders.name">
            <send-all-providers></send-all-providers>
        </popup>

        <popup :show="popups.userBan.show"
               :is-set-styled="true"
               :name="popups.userBan.name">
            <ban-user></ban-user>
        </popup>
        <popup :show="popups.userUnBan.show"
               :is-set-styled="true"
               :name="popups.userUnBan.name">
            <un-ban-user></un-ban-user>
        </popup>


        <category-choice v-if="showMenuRightIcon" :show="popups.categoryChoice.show"
                         :name="popups.categoryChoice.name"></category-choice>
    </div>
</template>
<script>
    import {mapState} from 'vuex';
    import TopMenu from '@/components/TopMenu.vue'
    import Popup from '@/components/modal/Popup.vue'
    import SendAllProviders from '@/components/modal/SendAllProviders.vue'
    import BanUser from '@/components/modal/BanUser.vue'
    import UnBanUser from '@/components/modal/UnBanUser.vue'
    import Auth from '@/components/user/auth/AuthPersonal.vue'
    import Tooltip from '@/components/Tooltip.vue'
    import ActionMenu from '@/components/moderator/ActionMenu.vue'
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'
    import CategoryChoice from '@/components/forms/CategoryChoice.vue'
    import {scrollTo} from '@/helpers/animate.js';
    import VueFooter from '@/components/VueFooter.vue'

    export default {
        components: {
            BanUser,
            UnBanUser,
            TopMenu,
            CategoryChoice,
            Popup,
            SendAllProviders,
            Auth,
            Tooltip,
            ActionMenu,
            SubmitBtn,
            VueFooter
        },
        created() {
            this.$store.dispatch('users/getCurrent').then(()=>{
                if(!this.successModerator) {
                    if(this.user.permissionModerator.indexOf(19) !== -1){
                        this.$router.push({path: '/moderator/repayment/'});
                    }
                }
            });
            this.$store.dispatch('cities/setCityCookies');
            this.$store.dispatch('cities/getCities');
            this.$store.dispatch('options/getList');
            //this.$store.dispatch('menus/getActionMenu',{type:'moderator'});
            if(window.location.hash === '#open_catalog' && this.successModerator){
                this.openCategoryChoice()
            }
        },
        mounted(){
            this.$store.subscribe((mutation, state) => {
                if(mutation.type=='popups/open'){
                    if(state.popups.items.notifyPageReset.show==true){
                        this.showNotifyPageReset();
                    }
                }
            });

            let scrollWidth = window.innerWidth - document.documentElement.clientWidth;
            if (scrollWidth == '0') {
                document.getElementsByTagName('body')[0].classList.add("scroll-mac-os");
            }
        },
        data(){
            return {
                buttons: {
                    saveAction: {
                        state: 'normal'
                    },
                    savePartner: {
                        state: 'normal'
                    },
                    approveEdit:{
                        state: 'normal',
                    },
                    rejectEdit:{
                        state: 'normal',
                    }
                }
            }
        },
        computed: {
            isCooperationEdit() {
                return this.$route.name === 'cooperation-edit'
            },
            isActionEditPage() {
                return this.$route.name === 'coupons-edit'
            },
            isCooperationEditLoaded() {
                if ((this.cooperationItem.length !== 0) && !this.cooperationLoading) {
                    return true;
                } else {
                    return false;
                }
            },
            isCouponDetail(){
                return this.$route.name === 'coupons-detail'
            },
            isRepaymentDetail(){
                return this.$route.name === 'repayment-detail'
            },
            isProviderEdit() {
                return this.$route.name === 'provider-edit'
            },
            isProviderConstruct() {
                return this.$route.name === 'coupons-about-edit' || this.$route.name === 'providers-about-edit'
            },
            cancelProviderAboutEdit(){
                if(this.$route.name === 'coupons-about-edit'){
                    return '/moderator/coupons/'+this.actionBackCode+'/id'+this.$route.params.id+'/'
                } else {
                    return '/moderator/providers/'+this.providerBackCode+'/id'+this.$route.params.id+'/'
                }
            },
            isProviderEditLoaded() {
                if (!this.providerLoading) {
                    return true;
                } else {
                    return false;
                }
            },
            isCouponOperationsLoaded(){
                if ((this.operations.length !== 0) && !this.couponLoading) {
                    return true;
                } else {
                    return false;
                }
            },
            isRepaymentDetailLoaded() {
                if ((this.repayment.length !== 0) && !this.repaymentLoading) {
                    return true;
                } else {
                    return false;
                }
            },
            isShowMenu() {
                return (!(this.$route.name === 'cooperation-edit') &&
                        !(this.$route.name === 'provider-edit') &&
                        !(this.$route.name === 'providers-about-edit') &&
                        !(this.$route.name === 'coupons-about-edit') &&
                        !(this.$route.name === 'coupons-detail') &&
                        !(this.$route.name === 'coupons-edit') &&
                        !(this.$route.name === 'repayment-detail'));
            },
            cooperationBackCode() {
                let statusId = this.cooperationListStatus ? this.cooperationListStatus : this.cooperationItem['status'];

                for (let itemIndex in this.actionMenuList['cooperation']) {
                    if (this.actionMenuList['cooperation'][itemIndex]['id'] == statusId) {
                        return this.actionMenuList['cooperation'][itemIndex]['code'];
                    }
                }
            },
            cooperationBackId() {
                let statusId = this.cooperationListStatus ? this.cooperationListStatus : this.cooperationItem['status'];

                return statusId;
            },
            cooperationBackName() {
                let statusId = this.cooperationListStatus ? this.cooperationListStatus : this.cooperationItem['status'];

                for (let itemIndex in this.actionMenuList['cooperation']) {
                    if (this.actionMenuList['cooperation'][itemIndex]['id'] == statusId) {
                        return this.actionMenuList['cooperation'][itemIndex]['name'] + ' заявки';
                    }
                }
            },
            providerBackCode() {
                let statusId = this.providerListStatus ? this.providerListStatus : this.providerItem['status'];

                for (let itemIndex in this.actionMenuList['providers']) {
                    if (this.actionMenuList['providers'][itemIndex]['id'] == statusId) {
                        return this.actionMenuList['providers'][itemIndex]['code'];
                    }
                }
            },
            providerBackId() {
                let statusId = this.providerListStatus ? this.providerListStatus : this.providerItem['status'];

                return statusId;
            },
            providerBackName() {
                let statusId = this.providerListStatus ? this.providerListStatus : this.providerItem['status'];

                for (let itemIndex in this.actionMenuList['providers']) {
                    if (this.actionMenuList['providers'][itemIndex]['id'] == statusId) {
                        return this.actionMenuList['providers'][itemIndex]['title'];
                    }
                }
            },
            providerDescr() {
                for (let itemIndex in this.actionMenuList['providers']) {
                    if (this.actionMenuList['providers'][itemIndex]['id'] == this.providerItem['status']) {
                        return this.actionMenuList['providers'][itemIndex]['descr'];
                    }
                }
            },
            pageRoute() {
                return this.$route.name;
            },
            userCheck() {
                if (!!this.user.firstName && !!this.user.secondName && !!this.user.lastName && !!this.user.email) {
                    return true;
                }
                return false;
            },
            providerContractLink(){
                let link = '';
                if (this.isProviderEdit) {
                    link = this.$store.getters['moderatorProviders/GetContractLink'];
                }
                return link;
            },
            checkInnOgrn(){
                let isSuccess = this.$store.getters['moderatorProviders/GetCheckedStatusRequisites'];
                return isSuccess;
            },
            isModerator(){
                if(this.user.access_rights){
                    return this.user.access_rights.is_moderator;
                }
                else{
                    return false;
                }
            },
            repaymentBackCode() {
                let statusId = this.repaymentListStatus ? this.repaymentListStatus : this.repayment['moderator_action'];

                for (let itemIndex in this.actionMenuList['repayment']) {
                    if (this.actionMenuList['repayment'][itemIndex]['id'] == statusId) {
                        return this.actionMenuList['repayment'][itemIndex]['code'];
                    }
                }
            },
            repaymentBackId() {
                let statusId = this.repaymentListStatus ? this.repaymentListStatus : this.repayment['moderator_action'];

                return statusId;
            },
            repaymentBackName(){
                let statusId = this.repaymentListStatus ? this.repaymentListStatus : this.repayment['moderator_action'];

                for (let itemIndex in this.actionMenuList['repayment']) {
                    if (this.actionMenuList['repayment'][itemIndex]['id'] == statusId) {
                        return this.actionMenuList['repayment'][itemIndex]['title'];
                    }
                }
            },
            actionBackCode() {
                let statusId = this.actionListStatus ? this.actionListStatus : this.action.status_moderator;

                for (let itemIndex in this.actionMenuList['coupons']) {
                    if (this.actionMenuList['coupons'][itemIndex]['id'] == statusId) {
                        return this.actionMenuList['coupons'][itemIndex]['code'];
                    }
                }
            },
            actionBackId() {
                let statusId = this.actionListStatus ? this.actionListStatus : this.action.status_moderator;

                return statusId;
            },
            actionBackName(){
                let statusId = this.actionListStatus ? this.actionListStatus : this.action.status_moderator;

                for (let itemIndex in this.actionMenuList['coupons']) {
                    if (this.actionMenuList['coupons'][itemIndex]['id'] == statusId) {
                        return this.actionMenuList['coupons'][itemIndex]['name'];
                    }
                }
            },
            repaymentTitleRight(){
                for (let itemIndex in this.actionMenuList['repayment']) {
                    if (this.actionMenuList['repayment'][itemIndex]['id'] == this.repayment['moderator_action']) {
                        return this.actionMenuList['repayment'][itemIndex].description;
                    }
                }
            },
            token(){
                return this.localStorage.token !== 'false' && this.localStorage.token !== null && this.localStorage.token !== undefined ? this.localStorage.token : false
            },
            successModerator(){
                let result = true;
                if(this.user.permissionModerator !== undefined && this.user.permissionModerator.length > 0){
                    result = false;
                    switch (this.$route.name){
                        case 'repayment':
                        case 'repayment-detail':
                        case 'repayment-default':
                            if(this.user.permissionModerator.indexOf(19) !== -1){
                                result = true;
                            }
                            break;
                    }
                }
                return result
            },
            showMenuRightIcon(){
                let result = true;
                if(this.user.permissionModerator !== undefined && this.user.permissionModerator.length > 0){
                    if(this.user.permissionModerator.indexOf(19) !== -1){
                        result = false;
                    }
                }
                return result
            },
            ...mapState({
                popups: state => state.popups.items,
                user: state => state.users.user,
                formAuthFields: state => state.auth.forms.auth.fields,
                isValidPhone: state => state.auth.isValidPhone,
                cooperationItem: state => state.moderatorCooperation.itemDetail,
                cooperationLoading: state => state.moderatorCooperation.isLoad,
                providerItem: state => state.moderatorProviders.itemDetail,
                providerListStatus: state => state.moderatorProviders.listStatus,
                repaymentListStatus: state => state.moderatorRepayment.listStatus,
                actionListStatus: state => state.moderatorCoupon.listStatus,
                cooperationListStatus: state => state.moderatorCooperation.listStatus,
                providerLoading: state => state.moderatorProviders.isLoad,
                couponLoading: state => state.moderatorCoupon.isLoadDetail,
                actionMenuList: state => state.menus.itemsAction,
                isBanned: state => state.auth.isBanned,
                cookiesWarning: state => state.users.cookiesWarning,
                currentCity: state => state.cities.current,
                currentCityIp: state => state.cities.currentCityIp,
                operations: state => state.moderatorCoupon.operations,
                providerOperations: state => state.moderatorProviders.operations,
                action: state => state.moderatorCoupon.itemDetail,
                isUserLoad: state => state.users.isLoaded,
                repayment: state => state.moderatorRepayment.itemDetail,
                repaymentLoading: state => state.moderatorRepayment.isLoadDetail,
                repaymentOperations: state => state.moderatorRepayment.operations,
                currentStep: state => state.partnerActions.currentStep,
                status: state => state.moderatorCoupon.status,
            }),
        },
        methods: {
            openPrintContractError: function() {
                this.$store.dispatch('popups/open', {name: this.popups.printContract.name});
            },
            openAuthorization: function () {
                this.$store.dispatch('auth/setIsRedirect', true);
                this.$store.dispatch('popups/open', {name: this.popups.publicAuth.name});
            },
            openRequestApprove: function () {
                this.$store.dispatch('popups/open', {name: this.popups.requestApprove.name});
            },
            openRequestReject: function () {
                this.$store.dispatch('popups/open', {name: this.popups.requestReject.name});
            },
            openRequestPause: function () {
                this.$store.dispatch('popups/open', {name: this.popups.requestPause.name});
            },
            routeCouponEdit: function($id){
                this.$router.push({path: '/moderator/coupons/edit'+$id+'/'});
            },
            routeCouponSection($id){
                this.$router.push({path: '/moderator/coupons/section'+$id+'/'});
            },
            openCouponBlock: function(){
                this.$store.dispatch('popups/open', {name: this.popups.moderatorCouponBlock.name});
            },
            openCouponUnBlock: function(){
                this.$store.dispatch('popups/open', {name: this.popups.moderatorCouponUnBlock.name});
            },
            openCategoryChoice: function(isSetCategory) {
                if (this.$router.history.current.name === 'coupons-detail' || !!isSetCategory) {
                    this.$store.dispatch('moderatorCategories/setItem', {
                        id: this.action.id,
                        name: this.action.name
                    });
                }

                this.$store.dispatch('popups/open', {name: this.popups.categoryChoice.name});
                this.$nextTick(()=>{
                    document.getElementsByTagName('body')[0].classList.add("city-select");
                }
                );
            },
            openCouponApprove: function(){
                if(this.action.sections.length == 0){
                    this.$store.dispatch('popups/open', {name: this.popups.moderatorCouponAlert.name});
                }
                else{
                    this.$store.dispatch('popups/open', {name: this.popups.moderatorCouponApprove.name});
                }
            },
            openCouponReject: function(){
                this.$store.dispatch('popups/open', {name: this.popups.moderatorCouponReject.name});
            },
            openModalSendAllProviders: function(){
                this.$store.dispatch('popups/open', {name: this.popups.sendAllProviders.name});
            },
            openProviderPopup: function (popup) {
                let self = this,
                    popupActions = {
                    reject: function () {
                        self.$store.dispatch('popups/open', {name: self.popups.providerReject.name});
                    },
                    approve: function () {
                        self.$store.dispatch('popups/open', {name: self.popups.providerApprove.name});
                    },
                    print: function () {
                        self.$store.dispatch('popups/open', {name: self.popups.providerPrint.name});
                    },
                    check_send: function () {
                        self.$store.dispatch('popups/open', {name: self.popups.providerCheckSend.name});
                    },
                    final_approve: function () {
                        self.$store.dispatch('popups/open', {name: self.popups.providerFinalApprove.name});
                    },
                    block: function () {
                        self.$store.dispatch('popups/open', {name: self.popups.providerBlock.name});
                    },
                    cancel_reject: function () {
                        self.$store.dispatch('popups/open', {name: self.popups.providerCancelReject.name});
                    },
                    cancel_block: function () {
                        self.$store.dispatch('popups/open', {name: self.popups.providerCancelBlock.name});
                    },
                    overtime: function () {
                        self.$store.dispatch('popups/open', {name: self.popups.providerOvertime.name});
                    },
                }

                popupActions[popup]();
            },
            openCouponCheating: function(){
                this.$store.dispatch('popups/open', {name: this.popups.couponCheating.name});
            },
            openCouponExtend: function(){
                this.$store.dispatch('popups/open', {name: this.popups.couponExtend.name});
            },
            openCouponAnnul: function() {
                this.$store.dispatch('popups/open', {name: this.popups.couponAnnul.name});
            },
            openUserBan: function() {
                this.$store.dispatch('popups/open', {name: this.popups.userBan.name});
            },
            openUserUnBan: function() {
                this.$store.dispatch('popups/open', {name: this.popups.userUnBan.name});
            },
            rejectEdit: function(){
                this.$store.dispatch('popups/open', {name: this.popups.moderatorCouponRejectEdit.name});
                /*this.buttons.rejectEdit.state = 'load';
                this.$store.dispatch('moderatorCoupon/sendForm', {
                    type: 'rejectEdit',
                    id: this.action.id,
                }).then(()=>{
                    this.buttons.rejectEdit.state = 'ok';
                    this.$store.dispatch('moderatorCoupon/initDetailPage',{route: this.$route});
                });*/
            },
            approveEdit: function(){
                this.$store.dispatch('popups/open', {name: this.popups.moderatorCouponApproveEdit.name});
                /*this.buttons.approveEdit.state = 'load';
                this.$store.dispatch('moderatorCoupon/sendForm', {
                    type: 'approveEdit',
                    id: this.action.id,
                }).then(()=>{
                    this.buttons.approveEdit.state = 'ok';
                    this.$store.dispatch('moderatorCoupon/initDetailPage',{route: this.$route});
                });*/
                //this.$store.dispatch('popups/open', {name: this.popups.couponAnnul.name});
            },
            logout() {
                this.$store.dispatch('auth/logout');
                this.$store.commit('auth/setStep',{ step:1 })
                //this.$store.commit('partner/setLoaded',false)
                /*this.$store.commit('partner/setCurrentField', {
                    name: 'success',
                    value:false,
                })*/
            },
            saveAction() {
                if(this.currentStep !== 'finish'){
                    this.$store.dispatch('partnerActions/nextStep')
                } else {
                    this.buttons.saveAction.state = 'load';

                    this.$store.dispatch('partnerActions/saveAction',{id:this.$route.params.id,isModerator: true}).then(()=> {
                        this.buttons.saveAction.state = 'ok';
                        setTimeout(()=>{
                            this.$router.push('/moderator/coupons/'+this.actionBackCode+'/id'+this.$route.params.id);
                            this.buttons.saveAction.state = 'normal';
                        }, 300);
                    }, ()=> {
                        this.buttons.saveAction.state = 'normal';
                    })
                }
            },
            saveAboutForm() {
                this.buttons.savePartner.state = 'load';
                this.$store.dispatch('partner/saveAboutForm',{isModeraite: true}).then((data)=>{
                    this.buttons.savePartner.state = 'ok';
                    setTimeout(()=>{
                        if(this.$route.name === 'coupons-about-edit'){
                            this.$router.push('/moderator/coupons/'+this.actionBackCode+'/id'+this.$route.params.id);
                        } else {
                            this.$router.push('/moderator/providers/'+this.providerBackCode+'/id'+this.$route.params.id);
                        }
                        if(data.isShowModalAboutPage){
                            this.$store.dispatch('popups/open', {name: this.popups.constructPartnerFinal.name});
                        }
                        this.buttons.savePartner.state = 'normal';
                    }, 300);

                }).catch((e)=>{
                    if(!!e.scrollTo) {
                        if(e.scrollTo == 'otherTitle'){
                            scrollTo('#scroll-other');
                        } else {
                            scrollTo('#scroll-'+e.scrollTo);
                        }

                    }
                });
            },
            showNotifyPageReset(){
                this.$refs.tooltipNotifyReset.tooltipShow();
            }
        }
    }
</script>
<style lang="sass">
    body
</style>
