import axios from 'axios'
import {jsonHeader, wwwFormHeader} from '@/helpers/axiosHeaders'
import Vue from 'vue'
import qs from "qs";

const state = {
    itemId: false,
    itemName: false,
    loadStatus: '',
    itemSaves: false,
    onClose: true,
}

const getters = {}

const mutations = {
    setItem(state, data) {
        Vue.set(state,'itemId', data.id);
        Vue.set(state,'itemName', data.name);
    },
    setClose(state, data){
        if(state.itemSaves){
            state.onClose = true;
        }
    },
    setItemSaves(state, data){
        state.itemSaves = data;
    },
    setLoadStatus(state, data) {
        state.loadStatus = data;
    },
    setToken(state, data) {
        state.token = data;
    },
}
const actions = {
    getToken(context, data) {
        return localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false;
    },
    setItem(context, data) {
        context.commit('setItem', data);
    },
    setLoadStatus(context, data) {
        context.commit('setLoadStatus', data);
    },
}
export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}