 import Vue from 'vue'
import Vuex from 'vuex'
import menus from './modules/menus'
import popups from './modules/popups'
import auth from './modules/auth'
import users from './modules/users'
import cities from './modules/cities'
import moderatorCooperation from './modules/moderator/cooperation'
import moderatorProviders from './modules/moderator/providers'
import moderatorCategories from './modules/moderator/categories'
import moderatorCoupon from './modules/moderator/coupon'
import moderatorTemplates from './modules/moderator/templates'
import moderatorCounters from './modules/moderator/counters'
import moderatorNotes from './modules/moderator/notes'
import moderatorRepayment from './modules/moderator/repayment'
import masks from './modules/masks'
import partnerActions from './modules/partner/actions'
import partner from './modules/partner/partner'
import opinions from './modules/opinions'
import options from './modules/options'
import queries from './modules/moderator/queries'
import moderatorStatistic from './modules/moderator/statistic'
import promotion from './modules/moderator/promotion'

Vue.use(Vuex)

export default new Vuex.Store({
    strict: process.env.NODE_ENV !== 'production',
    modules: {
        menus,
        popups,
        auth,
        users,
        cities,
        moderatorCooperation,
        moderatorProviders,
        moderatorCategories,
        moderatorNotes,
        moderatorCoupon,
        moderatorTemplates,
        moderatorCounters,
        moderatorRepayment,
        masks,
        partnerActions,
        partner,
        opinions,
        options,
        queries,
        moderatorStatistic,
        promotion,
    },
    state: {
        onTabClick: false,
        notifyPageVersion: '',
    },
    mutations: {
        setTabClick(state, data){
            state.setTabClick = true;
        },
        setNotifyPageVersion(state, data){
            state.notifyPageVersion = data;
        },
        setSearchFocus(state, data){
            state.setSearchFocus = true;
        },
    },
    actions: {

    }
})
