<template>
    <transition name="fade-popup">
        <div class="city-select-container" v-if="show">
            <header class="header">
                <div class="header-main header-main--editor">
                    <div class="inner-wrapper">
                        <div class="h-left">

                        </div>

                        <div class="h-right">

                            <span class="text-color-white-trans text-size-s mr24" v-show="loadStatus === 'load'">Сохранение...</span>

                            <transition name="fade-status">
                                <span class="text-color-white-trans text-size-s mr24" v-show="loadStatus === 'ok'">Изменения сохранены</span>
                            </transition>

                            <transition name="fade-status">
                                <span class="text-color-white-trans text-size-s mr24" v-show="loadStatus === 'error'">Ошибка сохранения</span>
                            </transition>

                            <span @click="close" class="btn h-editor-btn btn--ico btn--save">
                                <span class="i i-ok2"></span>
                                <span class="t">Готово</span>
                            </span>
                        </div>
                    </div>
                </div>
            </header>



            <div class="content">
                <div class="main-title main-title--nowrap">
                    <div class="main-title__wrap">
                        <div class="h1" v-if="itemName">Добавить в каталог <span class="text-color-link">«</span><span class="main-title__search-link">{{ itemName }}</span>»</div>
                        <div class="h1" v-else>Работа с каталогом</div>
                    </div>
                </div>


                <categories class="category-tree-container mt32" :class="{disabled: loadStatus === 'load'}"></categories>
            </div>
        </div>
    </transition>
</template>

<script>


    import {mapState} from 'vuex';
    import Categories from '@/components/moderator/Categories.vue';

    export default {
        name: "CategoryChoice",
        components: {
            Categories
        },
        props: {
            regionsProp: {},
            show: {
                default: function () {
                    return false
                }
            },
            name: '',
        },
        data: function() {
            return {
                regions: {},
                countSelect: 0,
                isShowPopup: false,
            }
        },
        mounted: function() {
        },

        computed: {
            ...mapState({
                itemName: state => state.moderatorCategories.itemName,
                loadStatus: state => state.moderatorCategories.loadStatus,
                actionMenuList: state => state.menus.itemsAction,
            }),
        },

        methods: {
            close: function () {
                if(this.show && this.name){
                    this.$store.commit('moderatorCategories/setClose',true);
                    this.$store.commit('moderatorCategories/setItem', {});
                    this.$store.commit('moderatorCategories/setItemSaves', false);
                    this.$store.commit('popups/close', {name: this.name});

                    if (this.$router.history.current.name === 'coupons') {

                        let action_status = this.actionMenuList['coupons'][0].id;

                        if ((+this.$router.history.current.params.action > 0)) {
                            action_status = (+this.$router.history.current.params.action);
                        }

                        this.$store.dispatch('moderatorCoupon/getItems', {
                            status: action_status,
                            reload: true
                        }).then(() => {
                            setTimeout(() => {
                                if (this.scrollTop) {
                                    document.body.scrollTop = document.documentElement.scrollTop = this.scrollTop;
                                    this.$store.commit('moderatorCoupon/setNumPage', this.pageCount + 1);
                                }
                                this.$store.commit('moderatorCoupon/setBackData', {
                                    scrollTop: 0,
                                    pageCount: 0
                                });
                            }, 50);
                        });
                    }
                    
                    document.getElementsByTagName('body')[0].classList.remove("city-select")
                }
                
            },
        }
    }
</script>

<style scoped>
    .fade-status-enter-active, .fade-status-leave-active {
        transition: opacity .3s;
    }
    .fade-status-enter, .fade-status-leave-to {
        opacity: 0;
    }
    .category-tree-container.disabled:before {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: 99999;
    }

</style>