<template>
    <div>
        <div class="popup__content">
            <div class="h1 mb8" style="font-size: 31px; ">Удалить раздел каталога?</div>
            <p>
                Раздел, который вы хотите удалить, содержит
                <span style="font-weight:bold;" v-html="countSections"></span>&nbsp;<span style="font-weight:bold;"
                                                                                          v-html="mfWord(countSections, ['вложенный раздел', 'вложенных раздела', 'вложенных разделов'])"></span>
                <br>и
                <span style="font-weight:bold;" v-html="countActions"></span>&nbsp;<span style="font-weight:bold;"
                                                                                         v-html="mfWord(countActions, ['акцию', 'акции', 'акций'])"></span>,
                которые будут перемещены в «Несортированные». Вы уверены?
            </p>

            <!-- end row-b -->
            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <button class="btn mr24" @click="apply">Я понимаю что делаю!</button>
                    <a href="#" @click.prevent="cancel" class="pseudo-link-cancel">Отмена</a>
                </div>


                <div class="row-b__col elem-pos-fit">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'

    export default {
        name: "CategoriesDelete",
        components: {},
        props: {
            countSections: {default: 0},
            countActions: {default: 0}
        },
        data: function () {
            return {}
        },
        created() {
        },
        mounted() {
        },
        methods: {
            apply: function () {
                this.$emit('apply');
            },
            cancel: function () {
                this.$emit('cancel');
            },
            mfWord: function (number, titles) {
                let cases = [2, 0, 1, 1, 1, 2];
                return titles[(number % 100 > 4 && number % 100 < 20) ? 2 : cases[(number % 10 < 5) ? number % 10 : 5]];
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            })
        }
    }
</script>

