<template>
    <div ref="searchField" class="header-category__search" v-clickoutside="hideSearchField">
        <div class="field-wrap">
            <input v-on:keyup.enter="fetchData" type="text" ref="searchFieldInput" class="field" placeholder="Поиск поставщиков" v-model="search">
            <span @click="resetSearch()" class="field-clear i i-close2"></span>
            <button class="b" @click="fetchData"></button>
        </div>
        
        <div ref="searchFieldBtn" @click="openSearchField" class="btn-search"></div>
    </div>
</template>



<script>

    import {mapState} from 'vuex';

    export default {
        name: "ProvidersSearch",
        components: {
        },
        data: function() {
            return {
                search: '',
                unsubscribe: '',
            }
        },
        mounted: function() {
            this.unsubscribe = this.$store.subscribe((mutation, state) => {
                if(mutation.type=='setSearchFocus'){
                    this.search = '';
                    this.openSearchField();
                }  
            });
        },
        beforeDestroy: function(){
            this.unsubscribe();    
        },
        computed: {
            ...mapState(
                {
                    menuList: state => state.menus.itemsAction,
                }),
            defaultStatus() {
                return this.menuList['providers'][0]['id'];
            },
            currentStatus() {
                return this.$route.params.action ? this.$route.params.action : this.defaultStatus;
            }
        },
        methods: {
            resetSearch() {
                this.search = '';
                this.hideSearchField();
                this.fetchData();
            },
            fetchData() {
                this.$store.dispatch('moderatorProviders/getItems', {status: this.currentStatus, search: this.search, reload: true});
            },
            openSearchField: function(){
                this.$refs.searchField.classList.add('open');
                this.$refs.searchFieldInput.focus();
            },
            hideSearchField: function() {
                this.$refs.searchField.classList.remove('open');
            }
            
        },
        created: function() {
            // document.addEventListener('click', e => {
            //     let target = e.target;
            //     let search = this.$refs.searchField;
            //     if (typeof(search) !== 'undefined') {
            //         let itsSearch = target == search || search.contains(target);

            //         if (!itsSearch) {
            //             this.hideSearchField();
            //         }
            //     }
            // })
        },
        
        directives: {
            'clickoutside': {
                bind: function(el, binding, vNode) {
                    if (typeof binding.value !== 'function') {
                        const compName = vNode.context.name;
                        let warn = `[Vue-click-outside:] provided expression '${binding.expression}' is not a function, but has to be`
                        if (compName) { warn += `Found in component '${compName}'` }
                        console.warn(warn);
                    }
                    const bubble = binding.modifiers.bubble;
                    const handler = (e) => {
                        if (bubble || (!el.contains(e.target) && el !== e.target) && (!e.target.classList.contains('click-outside-search'))) {
                            binding.value(e)
                        }
                    }
                    el.__vueClickOutside__ = handler;
                    document.addEventListener('click', handler);
                },
              
                unbind: function(el, binding) {
                    document.removeEventListener('click', el.__vueClickOutside__);
                    el.__vueClickOutside__ = null;
                }
            }
        }
    }
</script>