import axios from 'axios'
import {jsonHeader, wwwFormHeader} from '@/helpers/axiosHeaders'
import Vue from 'vue'
import qs from "qs";

const state = {
    items: [],
    itemsProvider: {},
    isLoadedProcess: true,
    isLastPage: false,
    sort: {
        current: {
            order: 'view',
            by: 'desc',
        },
        order: {
            view: {
                code: 'view',
            },
            coupons: {
                code: 'coupons',
            },
            repay: {
                code: 'repay',
            },
            sum: {
                code: 'sum',
            },
        },
        by: {
            asc: 'asc',
            desc: 'desc',
        }
    },
    page: 1,
}
const getters = {}
const mutations = {
    setSort(state, data) {
        if(data.order !== state.sort.current.order){
            Vue.set(state.sort.current, 'order', data.order);
            Vue.set(state.sort.current, 'by', 'desc');
        } else {
            if(state.sort.current.by === 'asc'){
                Vue.set(state.sort.current, 'by', 'desc');
            } else {
                Vue.set(state.sort.current, 'by', 'asc');
            }

        }
    },
    incrementPage(state) {
        Vue.set(state, 'page', state.page + 1);
    },
    resetPage(state) {
        Vue.set(state, 'page', 1);
        Vue.set(state, 'items', []);
        Vue.set(state, 'isLastPage', false);
    },
    setItems(state, data) {
        for (let ind in data.items) {
            if (data.items.hasOwnProperty(ind)) {
                state.items.push(data.items[ind]);
                if(!state.itemsProvider[data.items[ind].provider.id]){
                    Vue.set(state.itemsProvider, data.items[ind].provider.id, {items: [], isLoadedProcess: false});
                }
            }
        }
    },
    setLoadedProcess(state, bool) {
        Vue.set(state, 'isLoadedProcess', bool);
    },
    setLastPage(state, bool) {
        Vue.set(state, 'isLastPage', bool);
    },
    setItemsProviderLoading(state, data) {
        Vue.set(state.itemsProvider[data.id], 'isLoadedProcess', data.value);
    },
    setItemsProvider(state, data) {
        Vue.set(state.itemsProvider[data.id], 'items', data.items);
    },
}
const actions = {
    getActions(context, data){
        if (state.isLoadedProcess) {

            if (data.action === 'sort') {
                context.commit('resetPage');
                context.commit('setLastPage', false);
            }

            if (!state.isLastPage) {

                let query = {
                    sort: state.sort.current,
                    page: state.page,
                    status: data.tab
                };

                context.commit('setLoadedProcess', false);

                axios.get(process.env.VUE_APP_API_PATH+'/moderator/promotion/', {
                    headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                    params: query,
                    paramsSerializer: function(params) {
                        return qs.stringify(params, {arrayFormat: 'repeat'})
                    },
                    withCredentials: true,
                }).then(response => {
                    if (typeof response.data === 'object' && +response.data.error === 0) {
                        context.commit('setItems', response.data.result);
                        context.commit('incrementPage');

                        if (!response.data.result.isLoadNextPage || response.data.result.items.length === 0) {
                            context.commit('setLastPage', true);
                        }
                    }

                    context.commit('setLoadedProcess', true);
                }).catch(function (error) {
                    context.commit('setLoadedProcess', true);
                });
            }
        }
    },
    getActionsProvider(context, data){
        if (state.itemsProvider[data.id].items.length == 0 && !state.itemsProvider[data.id].isLoadedProcess) {

            let query = {
                providerId: data.id
            };
            context.commit('setItemsProviderLoading',{id: data.id, value:true});
            axios.get(process.env.VUE_APP_API_PATH+'/moderator/promotion/', {
                headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                params: query,
                paramsSerializer: function(params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
                withCredentials: true,
            }).then(response => {
                if (typeof response.data === 'object' && +response.data.error === 0) {
                    context.commit('setItemsProvider', {items:response.data.result.items,id:data.id});
                }
                context.commit('setItemsProviderLoading',{id: data.id, value:false});
            }).catch(function (error) {
                context.commit('setItemsProviderLoading',{id: data.id, value:false});
            });
        }

    },
}
export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}