<template>
    <div>
        <div class="popup__content">
            <div class="h1 mb8" style="font-size: 31px; ">Внимание!</div>
            
            <p>
                Вы пытаетесь создать подраздел в папке, содержащей <span style="font-weight:bold;" v-html="count"></span>&nbsp;<span style="font-weight:bold;" v-html="mfWord(count, ['акцию', 'акции', 'акций'])"></span>.<br>
                Для
                <span v-if="count > 1">них</span>
                <span v-else>неё</span>
                тоже будет создан подраздел, поэтому придумайте для него название:
                <input ref="section_name" type="text" v-model="sectionName" class="inp-s full size-s"
                       :class="{error: sectionNameError}"
                       placeholder="Название раздела">
            </p>

            <!-- end row-b -->
            
            
            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <button class="btn mr24" @click="apply">Подтвердить</button>
                    <a href="#" @click.prevent="cancel" class="pseudo-link-cancel">Отмена</a>
                </div>
                
                
                <div class="row-b__col elem-pos-fit">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'

    export default {
        name: "CategoriesCreate",
        components: {
        },
        props: {
            count: {default: 'несколько'}
        },
        data: function() {
            return {
                sectionName: '',
                sectionNameError: false
            }
        },
        created() {},
        mounted() {
            this.setFocus();
        },
        methods: {
            setFocus: function() {
                if (typeof (this.$refs['section_name']) !== 'undefined') {
                    this.$refs['section_name'].focus();
                }
            },
            apply: function () {
                if (this.sectionName) {
                    this.$emit('apply', {sectionName: this.sectionName});
                } else {
                    this.sectionNameError = true;
                    this.setFocus();
                }
            },
            cancel: function () {
                this.$emit('cancel');
            },
            mfWord: function (number, titles) {
                let cases = [2, 0, 1, 1, 1, 2];
                return titles[(number % 100 > 4 && number % 100 < 20) ? 2 : cases[(number % 10 < 5) ? number % 10 : 5]];
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            })
        }
    }
</script>

<style>
    .inp-s.error {
        border-bottom-color: #DB5A5A !important;
    }
</style>