import axios from 'axios'
import {jsonHeader,wwwFormHeader} from '@/helpers/axiosHeaders'
import Vue from 'vue'
import qs from "qs";

const state = {
    status:{
        new: 58,
        publick: 59,
        blocked: 61,
        invisible: 68,
        unsort: 62,
    },
    items: [],
    listStatus: 0,
    counters: [],
    itemDetail: {},
    couponImg: {},
    operations: [],
    provider: [],
    categories: [],
    itemDetailTemplates: [],
    isLoad: false,
    isLoadDetail: false,
    isLoadNextPage: true,
    isLoadSections: false,
    page: 1,
    backData: {
        scrollTop: 0,
        pageCount: 0
    }
}
const getters = {}
const mutations = {
    setBackData(state, data) {
        Vue.set(state, 'backData', data);
    },
    setNumPage(state, data) {
        state.page = data;
    },
    setListStatus(state, data) {
        if (typeof data === 'object' && data.listStatus !== undefined) {
            state.listStatus = data.listStatus;
        }
    },
    setItems(state, data) {
        if(typeof data === 'object' && data.items !== undefined) {
            //Vue.set(state,'items', data.items);
            for (let itemIndex in data.items) {
                let item = data.items[itemIndex];
                state.items.push(item); 
            }
        }
    },
    setCouponImg(state, data) { 
        //if (typeof data === 'object' && data.item !== undefined) {
            Vue.set(state,'couponImg', data);
        //}
    },
    setCategories(state, data){
        Vue.set(state,'categories', data);
    },
    setLoad(state, data) {
        state.isLoad = data;
    },
    setCounters(state, data){
        state.counters = data;
    },
    setOperations(state, data){
        state.operations = data;
    },
    setProvider(state, data){
        state.provider = data;
    },
    setNewPage(state){
        Vue.set(state,'items', []);     
        state.page = 1;
        state.isLoadNextPage = true;
    },
    incrementPage(state) {
        state.page++;
    },
    isLoadNextPage(state, data) {
        state.isLoadNextPage = data;
    },
    setCouponDetail(state, data) {
        if (typeof(state.itemDetail.statusModerator) === 'undefined') {
            Vue.set(state,'listStatus', data.item.statusModerator);
        }

        Vue.set(state, 'itemDetail', data.item);
        if(data.templates) {
            Vue.set(state, 'itemDetailTemplates', data.templates);
        } else {
            Vue.set(state, 'itemDetailTemplates', {});
        }
    },
    setLoadDetail(state, data) {
        state.isLoadDetail = data;
    },
    setLoadSections(state, data) {
        state.isLoadSections = data;
    },
    setItemSections(state, data){
        if(!data){
            state.itemDetail.sections = [];   
        }
        else{
            state.itemDetail.sections = data;    
        }
    }
}
const actions = {
    setBackData(context, data) {
        context.commit('setBackData', {
            scrollTop:  window.scrollY,
            pageCount: state.page - 1
        });
    },
    setListStatus(context, data) {
        context.commit('setListStatus', data);
    },
    getItems(context,data) {
        return new Promise((resolve, reject) => {
            //context.commit('setItems', {items:[]});
            if (!state.isLoad) {
                if (data.reload) {
                    context.commit('setNewPage', {status: data.status});
                }
                if (state.isLoadNextPage) {
                    context.commit('setLoad', true);

                    let params = {
                        status: data.status,
                        page: state.page,
                        pageCount: state.backData.pageCount
                    };

                    axios.get(process.env.VUE_APP_API_PATH + '/moderator/coupon/', {
                        headers: {
                            'Content-Type': 'application/json;charset=utf-8',
                            'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                        },
                        withCredentials: true,
                        params: params,
                        paramsSerializer: function (params) {
                            return qs.stringify(params, {arrayFormat: 'repeat'})
                        },
                    }).then(response => {
                        context.commit('setLoad', false);
                        if (typeof response.data === 'object' && +response.data.error === 0) {
                            context.commit('setItems', response.data.result);
                            context.commit('isLoadNextPage', response.data.result.isLoadNextPage);
                            if (response.data.result.counters) {
                                context.commit('setCounters', response.data.result.counters);
                            }
                        } else {
                            if (+response.data.error === 401) {
                                context.dispatch('auth/logout', {}, {root: true})
                            }
                        }
                        context.commit('incrementPage');
                        resolve();
                    }).catch(function (error) {
                        context.commit('setLoad', false);
                        reject();
                    });
                }
            }
        });
    },
    couponImg(context, data) {
        return new Promise((resolve, reject) => {
            let params = {
                type: 'getImg',
            };

            axios.get(process.env.VUE_APP_API_PATH+'/moderator/coupon/'+data.route.params.id,{
                withCredentials: true,
                params: params,
                paramsSerializer: function (params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
                headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
            }).then(response => {
                if(typeof response.data === 'object' && +response.data.error === 0) {
                    context.commit('setCouponImg', response.data.result.items);
                }   
            });
        });   
    },
    initDetailPage(context, data) {
        return new Promise((resolve, reject) => {
            if (typeof(state.itemDetail.statusModerator) !== 'undefined') {
                context.commit('setListStatus', {listStatus: state.itemDetail.statusModerator});
            }

            context.commit('setCouponDetail', {item:{}});
            context.commit('opinions/clearData', {}, {root:true});
            if(!state.isLoadDetail && data.route.params.id !== undefined){
                context.commit('setLoadDetail', true);
                axios.get(process.env.VUE_APP_API_PATH+'/moderator/coupon/'+data.route.params.id,{
                    withCredentials: true,
                    headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                }).then(response => {
                    context.commit('setLoadDetail', false);
                    if(typeof response.data === 'object' && +response.data.error === 0) {
                        context.commit('setCouponDetail', response.data.result);
                        if(response.data.result.operations){
                            context.commit('setOperations', response.data.result.operations);
                        }
                        if(response.data.result.provider){
                            context.commit('setProvider', response.data.result.provider);
                        }
                        if(response.data.result.categories){
                            context.commit('setCategories', response.data.result.categories);
                        }
                    } else {
                        if( +response.data.error===401){
                            context.dispatch('auth/logout',{},{root:true})
                        }
                    }
                    resolve()
                }).catch(function (error) {
                    context.commit('setLoadDetail', false);
                    resolve()
                });
            } else {
                resolve()
            }
        });
    },
    sendForm(context, data){
        return new Promise((resolve, reject) => {
            let formData = new FormData();

            formData.append('type', data.type);
            formData.append('comment', data.comment);
            formData.append('smsText', data.smsText);
            formData.append('emailTitle', data.emailTitle);
            formData.append('emailText', data.emailText);
            formData.append('smsCouponText', (data.smsCouponText)?data.smsCouponText:'');
            if(data.reasonBan){
                formData.append('reasonBan', data.reasonBan);
            }

            axios.post(process.env.VUE_APP_API_PATH+'/moderator/coupon/' + data.id + '/', formData, {
                headers: {
                    'Content-Type': 'application/json;charset=utf-8',
                    'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                },
                withCredentials: true
            }).then(response => {
                if(typeof response.data === 'object' && +response.data.error === 0) {
                    resolve();
                } else {
                    if( +response.data.error===401){
                        context.dispatch('auth/logout',{},{root:true})
                    }
                }
            }).catch(function (error) {
            });
        });
    },
    refreshSections(context, data){
        return new Promise((resolve, reject) => {
            if(data.id !== undefined){
                context.commit('setLoadSections', true);
                axios.get(process.env.VUE_APP_API_PATH+'/moderator/coupon/'+data.id,{
                    params: {
                        type: 'getSections',
                    },
                    withCredentials: true,
                    headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                }).then(response => {
                    context.commit('setLoadSections', false);
                    if(typeof response.data === 'object' && +response.data.error === 0) {
                        context.commit('setItemSections', response.data.result.sections);
                    } else {
                        if( +response.data.error===401){
                            context.dispatch('auth/logout',{},{root:true})
                        }
                    }
                }).catch(function (error) {
                    context.commit('setLoadSections', false);
                });
            }       
        });   
    },
}
export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}