<template>
    <text-editor
            v-model="valueEditor"
            :ref="refer"
            :editor-toolbar="toolbarSettings"
            :hide-toolbar="hideToolbar"
            :editor-options="editorSettings"
            :template-default="templateDefault"
            :placeholder-html="placeholderHtml"
            :set-target-blank="setTargetBlank"
            :class="{'error-editor':error,'hide-toolbar':hideToolbar, 'editor-focus': focus}"
            :placeholder="placeholder"
            @focus="onEditorFocus"
            @blur="onEditorBlur"
            @selection-change="onSelectionChange">
    </text-editor>
</template>

<script>
    import TextEditor from '@/components/editor/TextEditor.vue';
    export default {
        name: "TextEditorMin",
        components: {
            TextEditor
        },
        props: {
            id: {
                type: String,
                default: "quill-container"
            },
            placeholder: {
                type: String,
                default: ""
            },
            placeholderHtml: {
                type: String,
                default: ""
            },
            templateDefault: {
                type: String,
                default: ""
            },
            refer: {
                type: String,
                default: ""
            },
            valueDefault: {
                type: String,
                default: ""
            },
            hideToolbar: {
                type: Boolean,
                default: false
            },
            setTargetBlank: {
                type: Boolean,
                default: false
            },
            error: {
                type: Boolean,
                default: false
            },
            disabled: {
                type: Boolean
            },
        },

        data: function() {
            return {
                toolbarSettings: [
                    ["bold"],
                    [{ list: "bullet" }],
                ],
                editorSettings: {},
                valueEditor: '',
                focus: false,
            };
        },

        mounted: function() {
            this.$nextTick(function () {

                this.valueEditor = this.valueDefault.replace(/#GALLERY_(\d+)#/g, '').replace(/[\n\r]/g, '').replace(/(?:<p><br><\/p>){2,}/g,'<p><br></p>');
            });

        },


        methods: {
            onEditorBlur: function(quill) {
                this.$nextTick(function () {
                    this.focus = false;
                });
                this.$emit("blur");
            },
            onEditorFocus: function(quill) {
                this.$nextTick(function () {
                    this.$emit("error", false);
                    this.focus = true;
                });

            },
            onEditorReady: function(quill) {
            },
            onEditorChange: function(quill, html, text) {
            },
            onSelectionChange: function(range, oldRange, source) {
            },
        },

        watch: {
            valueEditor: function () {
                let newValue = this.valueEditor.replace(/<iframe srcdoc="<section[\s\S]*?id=&quot;surrogate_gallery_(\d+)[\s\S]*?\/iframe>/gi, '');
                newValue = newValue.replace(/[\n\r]/g, '').replace(/(?:<p><br><\/p>){2,}/g,'<p><br></p>');
                this.$emit("input", newValue);
                this.$emit("error", false);
            }
        }
    }
</script>

