<template>
    <div :class="{categoryEmpty:list.length == 0}">
        <draggable element="div"
                   :list="list"
                   @start="dragStart"
                   @end="dragSave"
                   :move="dragStatus"
                   v-if="typeof(list) !== 'undefined'"
                   :group="{ name: 'g1' }"
                   :draggable="'.category-draggable'"
                   :ghostClass="'sortable-ghost-custom'"
                   :chosenClass="'sortable-chosen-custom'"
                   :dragClass="'sortable-drag-custom'"
                   :dataIdAttr="'data-id'"
                   :filter="'.disable-drag-edit'"
                   :animation="150"
                   :touchStartThreshold="2"
                   :delay="50"
                   :disabled="disabled"
        >
            <div class="category-tree__item category-draggable" v-if="item.SECTIONS.length || !itemId"
                 :class="{open:item.OPENED, 'category-tree__item__parent': item.SECTIONS.length || true, 'category-tree__item__empty': item.SECTIONS.length === 0, 'hidden': item.ACTIVE == 'N'}"
                 :id="'item_'+item.ID"
                 :data-id="item.ID" v-show="item.VISIBLE"
                 v-for="(item, index) in list" :key="item.ID">
                <div class="title disable-drag"
                     :class="{'title--edit': item.IS_EDIT}">
                    <span @click.stop="clickSection(item.ID)" class="title__pointer" :class="{'disabled': item.ACTIVE == 'N'}"></span>
                    <span @click.stop="clickSection(item.ID)" class="title__hamburger disable-drag" :class="{'disabled': item.ACTIVE == 'N'}" v-if="item.ACTIVE !== 'N'"></span>
                    <span @click.stop="clickSection(item.ID)" class="i i-eye2 has-hint title__hidden" v-else>
                        <span class="hint bottom-right-direction">В скрытый раздел нельзя добавлять акции</span>
                    </span>

                    <span class="t" :class="{'disabled': item.ACTIVE == 'N'}" v-if="!item.IS_EDIT">
                    {{ item.NAME }}
                    </span>


                    <div class="wrap wrap-custom-input" :class="{'disabled': item.ACTIVE == 'N'}" v-if="item.IS_EDIT">
                        <div class="inp-wrap">
                            <input type="text"
                                   ref="editor"
                                   class="inp editor input_draggable disable-drag-edit full size-s"
                                   v-model.trim="item.TMP_NAME"
                                   @keyup.enter="applyChangesSection(item.ID)"
                                   @keyup.esc="toggleEditSection(item.ID)"
                                   @mouseenter="setFocus($event)"
                                   @focus="editFocus(item.ID)"
                                   @blur="editBlur(item.ID)"
                                   :class="{'focused': item.FOCUSED}"
                                   placeholder="Название раздела">
                        </div>

                        <div class="right">
                            <a href="#" @click="applyChangesSection(item.ID)"
                               class="btn btn--white btn--icon size-s w40">
                                <span class="i i-ok2"></span>
                            </a>

                            <a href="#" @click="toggleEditSection(item.ID)"
                               class="btn btn--white btn--icon size-s w40">
                                <span class="i i-close4"></span>
                            </a>
                        </div>
                    </div>
                    
                    
                    
                    <div class="category-tree__item__control">
                        <span class="b-list">
                            <span class="b has-hint btn_drag" v-if="showGrabBtn" :class="{'disabled': item.ACTIVE == 'N'}">
                                <span class="i i-dots"></span>

                                <span class="hint bottom-left-direction">Перетащить</span>
                            </span>

                            <span class="b has-hint" @click="deleteSection(item)" :class="{'disabled': item.ACTIVE == 'N'}">
                                <span class="i i-trash"></span>

                                <span class="hint bottom-left-direction">Удалить</span>
                            </span>
                            
                            <span class="b has-hint btn_eye disable-drag" @click="hideSection(item.ID)"
                                  :class="{unactive: item.ACTIVE == 'N', 'disabled': item.ACTIVE == 'N'}">
                                  
                                  <span class="hint bottom-left-direction">Скрыть</span>
                                <span class="i i-eye2"></span>
                            </span>

                            <span v-if="item.ACTIVE === 'N'" class="b has-hint btn_eye disable-drag unactive disabled" @click="hideSection(item)">

                                <span class="hint bottom-left-direction">Сделать видимым</span>
                                <span class="i i-eye3"></span>
                                
                            </span>
                            <span class="b has-hint btn_edit disable-drag" :class="{'disabled': item.ACTIVE == 'N'}" @click="toggleEditSection(item.ID)">
                                <span class="i i-pencil"></span>
                                
                                <span class="hint bottom-left-direction">Переименовать</span>
                            </span>
                            
                            <span class="b has-hint btn_add disable-drag" :class="{'disabled': item.ACTIVE == 'N'}" @click="formSubSection(item.ID)">
                                <span class="i i-plus2"></span>
                                
                                <span class="hint bottom-left-direction">Добавить подкатегорию</span>
                            </span>
                        </span>
                    </div>
                </div>

                <div class="item-hidden" v-show="item.OPENED || item.SECTIONS.length === 0">
                    <categories-items :list="item.SECTIONS" :itemId="itemId">
                    </categories-items>
                </div>
            </div>

            <div :id="'item_'+item.ID" :data-id="item.ID" v-else v-show="item.VISIBLE"
                 class="category-tree__item category-draggable open">
                <div class="item">
                    <div class="item__label" :class="{'title': item.IS_EDIT, 'title--edit': item.IS_EDIT}">
                        <input :id="'category-'+item.ID" type="checkbox" @click="selectedCategory(item.ID)"
                               v-model="item.CHECKED" v-if="item.ACTIVE !== 'N'">
                        <label :for="'category-'+item.ID" class="disable-drag">
                            <span @click.stop="clickSection(item.ID)" class="i i-eye2 has-hint" v-if="item.ACTIVE === 'N'">
                                <span class="hint bottom-right-direction">В скрытый раздел нельзя добавлять акции</span>
                            </span>
                            <span class="t" v-if="!item.IS_EDIT" :class="{'disabled': item.ACTIVE == 'N'}">{{ item.NAME }}</span>
                            <div class="wrap wrap-custom-input" v-if="item.IS_EDIT" :class="{'disabled': item.ACTIVE == 'N'}">
                                <div class="inp-wrap" :class="{'disabled': item.ACTIVE == 'N'}">
                                    <input type="text"
                                           onclick="this.focus();"
                                           v-model.trim="item.TMP_NAME"
                                           @mouseenter="setFocus($event)"
                                           @focus="editFocus(item.ID)"
                                           @blur="editBlur(item.ID)"
                                           @keyup="editKeyUp(item.ID, $event)" class="inp input_draggable disable-drag-edit full size-s"
                                           :class="{'focused': item.FOCUSED}"
                                           placeholder="Название раздела">
                                </div>

                                <div class="right" :class="{'disabled': item.ACTIVE == 'N'}" >
                                    <a href="#" @click="applyChangesSection(item.ID)"
                                       class="btn btn--white btn--icon size-s w40">
                                        <span class="i i-ok2"></span>
                                    </a>

                                    <a href="#" @click="toggleEditSection(item.ID)"
                                       class="btn btn--white btn--icon size-s w40">
                                        <span class="i i-close4"></span>
                                    </a>
                                </div>
                            </div>
                        </label>
                    </div>
                    
                    
                    <div class="category-tree__item__control">
                        <span class="b-list">
                            
                            <span class="b has-hint btn_drag" :class="{'disabled': item.ACTIVE == 'N'}">
                                <span class="i i-dots"></span>                     
                                
                                <span class="hint bottom-left-direction">Перетащить</span>
                            </span>
                            
                            <span class="b has-hint btn_eye" @click="hideSection(item.ID)"
                                  :class="{unactive: item.ACTIVE == 'N', 'disabled': item.ACTIVE == 'N'}">
                                  
                                  <span class="hint bottom-left-direction">Скрыть</span>
                                <span class="i i-eye2"></span>
                            </span>

                            <span class="b has-hint btn_eye" v-if="item.ACTIVE === 'N'" @click="hideSection(item)"
                                  :class="{unactive: item.ACTIVE == 'N', 'disabled': item.ACTIVE == 'N'}">

                                  <span class="hint bottom-left-direction">Сделать видимым</span>
                                <span class="i i-eye3"></span>
                            </span>
                            
                            <span class="b has-hint btn_edit" @click="toggleEditSection(item.ID)" :class="{'disabled': item.ACTIVE == 'N'}">
                                <span class="i i-pencil"></span>                     
                                
                                <span class="hint bottom-left-direction">Переименовать</span>
                            </span>
                            
                            <span class="b has-hint btn_add" @click="formSubSection(item.ID)" :class="{'disabled': item.ACTIVE == 'N'}">
                                <span class="i i-plus2"></span>                     
                                
                                <span class="hint bottom-left-direction">Добавить подкатегорию</span>
                            </span>
                        </span>
                    </div>
                </div>
                
                
                <categories-items :list="item.SECTIONS" :itemId="itemId">
                </categories-items>
            </div>
        </draggable>
    </div>
</template>

<script>

    import {mapState} from 'vuex';
    import draggable from '@/helpers/vuedraggable.js';
    let id = 1;
    export default {
        name: "CategoriesItems",
        display: "Simple",
        order: 0,
        components: {
            draggable
        },
        props: {
            showGrabBtn: {
                default: function () {
                    return false;
                }
            },
            list: {
                default: function () {
                    return []
                }
            },
            itemId: {
                default: function () {
                    return 0
                }
            },
            section: {default: false},
        },
        data: function () {
            return {
                drag: false,
                disabled: false,
            }
        },
        mounted: function () {
        },
        updated: function () {
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        },
        methods: {
            getPointerComponentCategories: function (parent) {

                if (parent.componentName === 'Categories') {
                    return parent;
                } else {
                    return this.getPointerComponentCategories(parent.$parent);
                }

            },
            enterSlideUp: function (el, done) {
                Velocity(el, "finish");

                Velocity(el,"slideDown", { duration: 300 }).then(function(e) {

                });
            },
            leaveSlideUp: function (el, done) {
                Velocity(el, "finish");
                Velocity(el,"slideUp", { duration: 100 }).then(function(e) {  });
            },
            setFocus: function (event) {
                event.target.focus();
            },
            editFocus: function (id) {
                this.disabled = true;
                if (id) {
                    this.setFocusClass(id);
                }
            },
            editBlur: function (id) {
                this.disabled = false;
                if (id) {
                    this.setUnFocusClass(id);
                }
            },
            dragSave: function (e) {
                let ComponentCategories = this.getPointerComponentCategories(this.$parent);

                clearTimeout(ComponentCategories.openSectionInterval);

                e.item.setAttribute('style', '');

                if (this.drag) {
                    let parentSection = ComponentCategories.getParent(ComponentCategories.sections, ComponentCategories.tmpEvent.draggedContext.element.ID, []);

                    if (parentSection.length === 0 || parentSection.ACTIVE === 'Y') {
                        if (parentSection.COUPONS) {
                            ComponentCategories.countCoupons = parentSection.COUPONS;
                            ComponentCategories.showModalDrag = true;
                            this.$store.dispatch('popups/open', {name: this.popups.categoriesMoveConfirm.name});
                        } else {
                            ComponentCategories.updateSections();
                        }
                    } else {
                        ComponentCategories.updateSections();
                    }

                    this.drag = false;
                }
            },
            dragStart: function (e) {
                let ComponentCategories = this.getPointerComponentCategories(this.$parent);
                let prevID = ComponentCategories.getPrevId(ComponentCategories.sections, e.item.getAttribute('data-id'));
                let nextID = ComponentCategories.getNextId(ComponentCategories.sections, e.item.getAttribute('data-id'));
                ComponentCategories.tmpPrevID = prevID;
                ComponentCategories.tmpNextID = nextID;

                let height = e.item.getAttribute('data-height');
                /*let ghost = $('.sortable-ghost-custom');

                if (ghost.length) {
                    ghost.css({'height': height});
                }*/
            },
            dragStatus: function (e) {
                let ComponentCategories = this.getPointerComponentCategories(this.$parent);
                let curSection = e.relatedContext.list[e.draggedContext.futureIndex];
                if (typeof(curSection) !== 'undefined') {
                    if (!curSection.OPENED) {
                        let _self = this;
                        clearTimeout(ComponentCategories.openSectionInterval);
                        ComponentCategories.openSectionInterval = setTimeout(function () {
                            _self.$set(ComponentCategories, 'sections', ComponentCategories.stateSection(ComponentCategories.sections, curSection.ID, 'OPENED'));
                        }, 1000);
                        //return false;
                    }
                }

                this.$set(ComponentCategories, 'tmpEvent', e);
                this.drag = true;
            },
            clickSection: function (id) {
                let ComponentCategories = this.getPointerComponentCategories(this.$parent);
                this.$set(ComponentCategories, 'sections', ComponentCategories.stateSection(ComponentCategories.sections, id, 'OPENED'));
            },
            setFocusClass: function (id) {
                let ComponentCategories = this.getPointerComponentCategories(this.$parent);
                this.$set(ComponentCategories, 'sections', ComponentCategories.stateSection(ComponentCategories.sections, id, 'FOCUSED'));
            },
            setUnFocusClass: function (id) {
                let ComponentCategories = this.getPointerComponentCategories(this.$parent);
                this.$set(ComponentCategories, 'sections', ComponentCategories.stateSection(ComponentCategories.sections, id, 'UNFOCUSED'));
            },
            formSubSection: function (id) {
                let ComponentCategories = this.getPointerComponentCategories(this.$parent);
                this.$set(ComponentCategories, 'sections', ComponentCategories.stateSection(ComponentCategories.sections, id, 'CREATE'));
                this.setEditorFocus();
            },
            setEditorFocus: function() {
                setTimeout(function () {
                    let editor_fields = window.document.getElementsByClassName('editor');

                    if (editor_fields.length) {
                        editor_fields[editor_fields.length - 1].focus();
                    }
                }, 100);
            },
            hideSection: function (id) {
                let ComponentCategories = this.getPointerComponentCategories(this.$parent),
                    section = ComponentCategories.getSectionById(ComponentCategories.sections, id);

                if (section.ACTIVE === 'N') {
                    ComponentCategories.stateSection(ComponentCategories.sections, section.ID, 'UNACTIVE');
                    ComponentCategories.updateSections();
                } else {
                    let countSubSections = ComponentCategories.getCountSubSections(section),
                        countCoupons = ComponentCategories.getCountCoupons(section);

                    if (countSubSections || countCoupons) {
                        this.$set(ComponentCategories, 'tmpHideID', section.ID);
                        this.$set(ComponentCategories, 'countCoupons', countCoupons);
                        this.$set(ComponentCategories, 'countSections', countSubSections);
                        this.$store.dispatch('popups/open', {name: this.popups.categoriesHideConfirm.name});
                    } else {
                        ComponentCategories.stateSection(ComponentCategories.sections, section.ID, 'UNACTIVE');
                        ComponentCategories.updateSections();
                    }
                }
            },
            deleteSection: function (section) {
                let ComponentCategories = this.getPointerComponentCategories(this.$parent),
                    countSubSections = ComponentCategories.getCountSubSections(section);

                if (section.DELETED === 'N') {
                    ComponentCategories.tmpDeleteID = section.ID;
                    if (section.COUPONS > 0 || countSubSections > 0) {
                        ComponentCategories.countCoupons = section.COUPONS;
                        ComponentCategories.countSections = ComponentCategories.getCountSubSections(section);
                        this.$store.dispatch('popups/open', {name: this.popups.categoriesDeleteConfirm.name});
                    }  else {
                        ComponentCategories.stateSection(ComponentCategories.sections, ComponentCategories.tmpDeleteID, 'DELETED');
                        ComponentCategories.updateSections();
                    }
                }
            },
            toggleEditSection: function (id) {
                let ComponentCategories = this.getPointerComponentCategories(this.$parent);
                this.$set(ComponentCategories, 'sections', ComponentCategories.stateSection(ComponentCategories.sections, id, 'EDIT'));
                this.setEditorFocus();
            },
            applyChangesSection: function (id) {
                let ComponentCategories = this.getPointerComponentCategories(this.$parent);
                let parentSection = ComponentCategories.getParent(ComponentCategories.sections, id, []);
                this.$set(ComponentCategories, 'sections', ComponentCategories.stateSection(ComponentCategories.sections, id, 'NAME'));
                this.toggleEditSection(id);

                let section = ComponentCategories.getSectionById(ComponentCategories.sections, id);

                if (section !== false && parentSection.COUPONS) {
                    ComponentCategories.countCoupons = parentSection.COUPONS;
                    ComponentCategories.tmpCreatedID = id;
                    ComponentCategories.showModalCreate = true;
                    this.$store.dispatch('popups/open', {name: this.popups.categoriesCreateConfirm.name});
                } else if (section !== false) {
                    ComponentCategories.updateSections();
                }
            },
            selectedCategory: function (id) {
                let ComponentCategories = this.getPointerComponentCategories(this.$parent);
                clearTimeout(window.timerDelay);

                window.timerDelay = setTimeout(function () {
                    ComponentCategories.updateElement();
                }, 1000);
            },
        }
    };
</script>
<style scoped>
    .buttons {
        margin-top: 35px;
    }
    .ghost {
        opacity: 0.5;
        background: red;
    }
    .category-tree__item:hover {
        position: relative;
        z-index: 1;
    }
    .category-tree__item__parent .item-hidden .categoryEmpty {
        padding-left: 48px;
    }
</style>