import axios from 'axios'
import {jsonHeader, wwwFormHeader} from '@/helpers/axiosHeaders'
import Vue from 'vue'
import qs from "qs";

const state = {
    items: [],
    listStatus: 0,
    itemDetail: {},
    itemDetailTemplates: [],
    isLoad: false,
    isLoadNextPage: true,
    operations: [],
    page: 1,
    searchText: '',
    isLoadSectionAction: false,
    backData: {
        scrollTop: 0,
        pageCount: 0
    }
}
const getters = {
    GetContractLink: state => {
        return '//api.zemskidki.ru' + state.itemDetail.contractLink;
    },
    GetCheckedStatusRequisites: state => {
        let isSuccess = true;

        if (state.itemDetail.requisitesArr) {
            if (typeof(state.itemDetail.requisitesArr['OGRN']) !== 'undefined' && !state.itemDetail.ogrn.checked) {
                isSuccess = false;
            }
            if (typeof(state.itemDetail.requisitesArr['OGRNIP']) !== 'undefined' && !state.itemDetail.ogrnip.checked) {
                isSuccess = false;
            }
            if (typeof(state.itemDetail.requisitesArr['INN']) !== 'undefined' && !state.itemDetail.inn.checked) {
                isSuccess = false;
            }
        }
        return isSuccess;
    },
}
const mutations = {
    setBackData(state, data) {
        Vue.set(state, 'backData', data);
    },
    setItems(state, data) {
        if (typeof data === 'object' && data.items !== undefined) {
            //Vue.set(state,'items', data.items);
            for (let itemIndex in data.items) {
                let item = data.items[itemIndex];
                state.items.push(item);
            }
        }
    },
    setListStatus(state, data) {
        if (typeof data === 'object' && data.listStatus !== undefined) {
            state.listStatus = data.listStatus;
        }
    },
    setOperations(state, data) {
        Vue.set(state, 'operations', data);
    },
    setItemDetail(state, data) {
        if (typeof(state.itemDetail.status) === 'undefined') {
            Vue.set(state, 'listStatus', data.item.status);
        }
        Vue.set(state, 'itemDetail', data.item);
        Vue.set(state, 'itemDetailTemplates', data.templates);
    },
    setLoad(state, data) {
        state.isLoad = data;
    },
    setSearchText(state, data) {
        state.searchText = data;
    },
    setNewPage(state) {
        Vue.set(state, 'items', []);
        state.page = 1;
        state.isLoadNextPage = true;
    },
    setNumPage(state, data) {
        state.page = data;
    },
    incrementPage(state) {
        state.page++;
    },
    isLoadNextPage(state, data) {
        state.isLoadNextPage = data;
    },
    setLoadSectionAction(data){
        state.isLoadSectionAction = data;
    },
    setItemSectionAction(state, data){
        for (let itemIndex in state.itemDetail.coupons) {
            if(state.itemDetail.coupons[itemIndex].id == data.id){
                //Vue.set(state.itemDetail.coupons[itemIndex],'sections_tree', data.result);
                state.itemDetail.coupons[itemIndex].sections_tree = data.result;
            }
        }
    },
    setItemActive(state, data) {
        Vue.set(state.items[data.index], 'itemActiveStat', data.value);
    },
    errorReq(context, data){
        Vue.set(state.itemDetail[data.type], 'error', data.value);
    },
}
const actions = {
    setListStatus(context, data) {
        context.commit('setListStatus', data);
    },
    setBackData(context, data) {
        context.commit('setBackData', {
            scrollTop:  window.scrollY,
            pageCount: state.page - 1
        });
    },
    getItems(context, data) {
        return new Promise((resolve, reject) => {
            context.commit('setItems', {items: []});
            if (!state.isLoad) {
                if (data.reload) {
                    context.commit('setNewPage', {status: data.status});
                }
                if (state.isLoadNextPage) {
                    context.commit('setLoad', true);

                    let params = {};

                    if (data.search) {
                        params.status = data.status;
                        params.search = data.search;
                    } else {
                        params.status = data.status;
                        params.page = state.page;
                        params.pageCount = state.backData.pageCount;
                    }

                    axios.get(process.env.VUE_APP_API_PATH + '/moderator/providers/', {
                        headers: {
                            'Content-Type': 'application/json;charset=utf-8',
                            'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                        },
                        withCredentials: true,
                        params: params,
                        paramsSerializer: function (params) {
                            return qs.stringify(params, {arrayFormat: 'repeat'})
                        },
                    }).then(response => {
                        context.commit('setLoad', false);
                        if (typeof response.data === 'object' && +response.data.error === 0) {
                            context.commit('setItems', response.data.result);
                            context.commit('setListStatus', {listStatus: response.data.result['moderator_action']});
                            context.commit('setSearchText', data.search);
                            context.commit('isLoadNextPage', response.data.result.isLoadNextPage);
                        } else {
                            if (response.data.message && response.data.message.success === false || +response.data.error === 401) {
                                context.dispatch('auth/logout', {}, {root: true})
                            }
                        }

                        context.commit('incrementPage');
                        resolve();
                    }).catch(function (error) {
                        context.commit('setLoad', false);
                        reject();
                    });
                }
            }
        });
    },
    getItem(context, data) {

        return new Promise((resolve, reject) => {
            if (typeof(state.itemDetail.status) !== 'undefined') {
                Vue.set(state, 'listStatus', state.itemDetail.status);
            }

            context.commit('setItemDetail', {item: []});
            if (!state.isLoad) {
                context.commit('setLoad', true);
                axios.get(process.env.VUE_APP_API_PATH_MB, {
                    headers: {
                        'Content-Type': 'application/json;charset=utf-8',
                        'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                    },
                    withCredentials: true,
                    params: {
                        ID: data.id,
                        GROUP: 'moderator',
                        RESOURCE: 'providers',
                    },
                    paramsSerializer: function (params) {
                        return qs.stringify(params, {arrayFormat: 'repeat'})
                    },
                }).then(response => {
                    context.commit('setLoad', false);
                    if (typeof response.data === 'object' && +response.data.error === 0) {
                        context.commit('setItemDetail', response.data.result);
                        if (response.data.result.item.operations) {
                            context.commit('setOperations', response.data.result.item.operations);
                        }
                        resolve();
                    } else {
                        if(response.data.message && response.data.message.success === false|| +response.data.error===401){
                            context.dispatch('auth/logout',{},{root:true})
                        }
                    }
                }).catch(function (error) {
                    context.commit('setLoad', false);
                    reject();
                });
            }
        });
    },

    sendForm(context, data){
        return new Promise((resolve, reject) => {
            let formData = new FormData();
            if(data.update){
                formData.append('update', data.update);
            }
            if(data.type){
                formData.append('type', data.type);
            }
            if(data.statuses){
                Object.keys(data.statuses).forEach(key => formData.append('statuses['+key+']', data.statuses[key]))
            }
            formData.append('comment', data.comment);
            formData.append('banText', data.banText);
            formData.append('smsUser', data.smsUser);
            formData.append('smsText', data.smsText);
            formData.append('emailTitle', data.emailTitle);
            formData.append('emailText', data.emailText);
            let path = process.env.VUE_APP_API_PATH+'/moderator/providers/';
            if(data.type !== 'send_all'){
                path += data.id + '/';
            }
            axios.post(path, formData, {
                headers: {
                    'Content-Type': 'application/json;charset=utf-8',
                    'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                },
                withCredentials: true
            }).then(response => {
                if(typeof response.data === 'object' && +response.data.error === 0) {
                    resolve();
                } else {
                    if(response.data.message && response.data.message.success === false|| +response.data.error===401){
                        context.dispatch('auth/logout',{},{root:true})
                    }
                }
            }).catch(function (error) {
            });
        });
    },
    sendLetter(context, data) {
        return new Promise((resolve, reject) => {

            let formData = new FormData();
            formData.append('type', data.type);
            formData.append('sendTo', data.sendTo);
            formData.append('smsText', data.smsText);
            formData.append('emailText', data.emailText);
            formData.append('emailTitle', data.emailTitle);


            axios.post(process.env.VUE_APP_API_PATH+'/moderator/letter/' + data.id + '/', formData, {
                headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                withCredentials: true,
            }).then(response => {
                if (typeof response.data === 'object' && +response.data.error === 0) {
                    resolve();
                } else {
                    if(response.data.message && response.data.message.success === false|| +response.data.error===401){
                        context.dispatch('auth/logout',{},{root:true})
                    }
                }
            }).catch(function (error) {
            });
        });
    },
    checkReq(context, data) {
        return new Promise((resolve, reject) => {
            axios.put(process.env.VUE_APP_API_PATH+'/moderator/providers/' + data.id + '/', {}, {
                headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                withCredentials: true,
                params: {
                    type: data.type,
                },
                paramsSerializer: function (params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
            }).then(response => {
                if (typeof response.data === 'object' && +response.data.error === 0) {
                    resolve();
                } else {
                    if(response.data.message && response.data.message.success === false|| +response.data.error===401){
                        context.dispatch('auth/logout',{},{root:true})
                    }
                }
            }).catch(function (error) {
            });
        });
    },
    refreshSectionAction(context, data) {
        return new Promise((resolve, reject) => {
            if(data.id !== undefined){
                context.commit('setLoadSectionAction', true);
                axios.get(process.env.VUE_APP_API_PATH+'/moderator/providers/'+data.id,{
                    params: {
                        type: 'refreshSectionAction',
                    },
                    withCredentials: true,
                    headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                }).then(response => {
                    context.commit('setLoadSectionAction', false);
                    if(typeof response.data === 'object' && +response.data.error === 0) {
                        context.commit('setItemSectionAction', {result: response.data.result.section, id:data.id});
                    } else {
                        if(response.data.message && response.data.message.success === false|| +response.data.error===401){
                            context.dispatch('auth/logout',{},{root:true})
                        }
                    }
                }).catch(function (error) {
                    context.commit('setLoadSectionAction', false);
                });
            }       
        }); 
    }
}
export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}